import { Message, MessageBox } from 'element-ui'
export default {
  checkResponseError (resp) {
    if (!resp) throw new Error('请求失败，请联系管理员。')
    if (!resp.data) throw new Error('请求失败，请联系管理员。')
    if (resp.data.timeout) throw new Error('请重新登录。')
    if (resp.data.errCode) throw new Error(resp.data.errMsg || '请求失败。')
  },
  alertError (e) {
    console.error(e)
    MessageBox(e.message, '提示', {
      closeOnClickModal: false,
      customClass: 'big-zindex',
      confirmButtonText: '确定'
    })
  },
  popError (e) {
    console.error(e)
    Message({
      customClass: 'big-zindex',
      showClose: true,
      message: e.message,
      type: 'error'
    })
  }
}



