<template>
  <div
    >{{ text }}<br>
    <el-select
      filterable
      value-key="activityDefId"
      size="mini"
      v-model="value">
      <el-option
        v-for="item in options"
        :key="item.activityDefId"
        :label="item.activityDefName"
        :value="item">
      </el-option>
    </el-select>
  </div>
</template>
<script>
export default {
  data () {
    return {
      value: null
    }
  },
  props: {
    selectValue: [Object, String],
    options: Array,
    text: String
  },
  watch: {
    value (val) {
      this.$emit('update:selectValue', val)
    },
    selectValue (val) {
      this.value = val
    }
  }
}
</script>
<style lang="scss" scoped>
/deep/ .el-input__inner{
  border-top-width: 0px;
  border-left-width: 0px;
  border-right-width: 0px;
  border-bottom-width: 0px;
  width: 200px;
  padding: 0px!important
}
/deep/ .el-input--mini .el-input__inner {
  font-size: 13px;
  font-weight: bolder;
  height: 16px;
  line-height: 16px;
  background-color: transparent;
  text-align: inherit;
  /* margin-right: 12px; */
  color: #409EFF;
}
/deep/ .el-input--mini .el-input__icon {
  line-height: 16px;
  height: 16px;
  font-size: 10px;
}
/deep/ .el-input--mini .el-input__suffix {
  right: 0px;
  width: 18px;
}

/deep/ input::-webkit-input-placeholder{
  color: #409EFF!important;opacity:1;
}

/deep/ input::-moz-placeholder{   /* Mozilla Firefox 19+ */
  color: #409EFF!important;opacity:1;
}

/deep/ input:-moz-placeholder{    /* Mozilla Firefox 4 to 18 */
  color: #409EFF!important;opacity:1;
}

/deep/ input:-ms-input-placeholder{  /* Internet Explorer 10-11 */
  color: #409EFF!important;opacity:1;
}
</style>



