<template>
  <!-- 模态窗内容 -->
  <div class="dialog-content">
    <!-- 当前环节 -->
    <div v-show="!isPass" style="font-size:12px;position:absolute;left:32px;top:-35px;color:#999999;">
      会签环节<br><div class="ellipsis" style="font-size:13px;width:100px;">{{ currentUserTask.activityDefName }}</div>
    </div>
    <!-- 背景图 -->
    <div v-show="!isPass" class="fly"><fly-svg></fly-svg></div>
    <!-- 下一环节 -->
    <div v-show="!isPass" style="font-size:12px;position:absolute;right:32px;top:-35px;text-align:right;color:#999999;"
      >下一环节<br
      ><strong class="ellipsis" style="font-size:13px;color:#666666;width:100px;"
      >会签图元</strong>
    </div>
    <div name="pass-content" v-if='!isPass'>
      <div class="wrapper">
        <vote-container
          :vote.sync="vote"
          :currentUserTask="currentUserTask"
          ref="container"
        />
      </div>
      <div class="opinion-input">
        <opinion-input
          :operationType="OperationType.POST"
          :placeholder="currentUserTask.firstActivity ? '请审批' : '同意'"
          :defaultOpinion="defaultOpinion"
          ref="opinionInput"
        ></opinion-input>
      </div>
      <!-- 其他会签意见 -->
      <el-collapse class="others-opinions-collapse" v-model="activeCollapse">
        <el-collapse-item name="1">
          <template slot="title"
            >其他会签者意见</template>
          <div v-if="!currentUserTask.hideSignOpinion">
            <el-row
              v-for="(opinion, index) in currentUserTask.signOpinions"
              :key="index">
              <el-col :span="4"><i class="imgicon-rule">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</i>{{opinion.userName}}</el-col>
              <el-col :span="7"><i class="imgicon-time">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</i>{{utils.strToDate(opinion.endTime, true, null, true)}}</el-col>
              <!-- <el-col :span="3"><i class="imgicon-yijian">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</i>{{opinionTypeConvert(opinion.opinionType)}}</el-col> -->
              <el-col :span="13"><i class="imgicon-liuyan">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</i>{{opinion.opinionContent}}</el-col>
            </el-row>
          </div>
        </el-collapse-item>
      </el-collapse>
    </div>
    <finish-page
      :passNames="nextAssignmentUserNames"
      v-if='isPass'>
    </finish-page>
  </div>
</template>

<script>
import AbstractPage from './mixins/AbstractPage'

import VoteContainer from '../containers/VoteContainer'
import FlySvg from '../components/svg/Fly'
import LastTask from '../components/LastTask/LastTask'
import LastTaskSvg from '../components/svg/LastTask'
import OpinionInput from '../components/OpinionInput/OpinionInput'
import FinishPage from '../components/FinishPage/FinishPage'

import TaskBizState from '../enumType/TaskBizState'
import OperationType from '../enumType/OperationType'
import SignOpinionType from '../enumType/SignOpinionType'
import CompletionState from '../enumType/CompletionState'

import ecpService from '../utils/ProxyService'
import utils from 'ecp.utils'
import Const from './Const'

function opinionTypeConvert (opinionType) {
  switch (opinionType) {
    default: return ''
    case SignOpinionType.AGREE: return '同意'
    case SignOpinionType.DISAGREE: return '不同意'
    case SignOpinionType.ABSTAIN: return '弃权'
  }
}

export default {
  mixins: [AbstractPage],
  components: {
    VoteContainer,
    FlySvg,
    OpinionInput,
    LastTask,
    LastTaskSvg,
    FinishPage
  },
  data () {
    return {
      utils,
      OperationType,
      TaskBizState,
      defaultOpinion: '',
      vote: SignOpinionType.AGREE
    }
  },
  props: {
  },
  methods: {
    /**
     * 请求数据
     */
    async queryFindCompleteNext (params) {
      console.debug('---sign task find complete next---')
      let { data } = await ecpService.doPost(Const.URL_findCompleteNext, params)
      console.debug(data)
      return data
    },
    async getParams () {
      if (this.vote === null) throw new Error('请选择意见')
      let { opinion } = await this.$_assignmentsAndOpinion()
      let params = {
        taskId: this.options.taskId,
        businessKey: this.options.businessKey,
        bizDataModel: this.options.bizDataModel,
        taskVariables: this.options.taskVariables,
        signOpinionType: this.vote,
        nextTaskAssignments: []
      }

      let data = await this.queryFindCompleteNext(params)
      let signCompleteState = data.signCompleteState
      let newActivities = data.nextActivities

      if (signCompleteState === CompletionState.ACHIEVE_PASS) {
        return { params, signCompleteState, newActivities }
      } else if (signCompleteState === CompletionState.ACHIEVE_ROLLBACK) {
        params.opinion = opinionTypeConvert(params.signOpinionType) + '：' + opinion
        return { params, signCompleteState }
      } else if (signCompleteState === CompletionState.NOT_ACHIEVE) {
        params.opinion = opinionTypeConvert(params.signOpinionType) + '：' + opinion
        return { params, signCompleteState }
      }
    },
    async sendParams (params) {
      let url = Const.URL_complete
      console.debug('---sending params---')
      console.debug(params)
      let resp = await ecpService.doPost(url, params)
      console.debug(resp.data)
      return resp.data
    }
  },
  watch: {
    vote (val) {
      switch (val) {
        case SignOpinionType.AGREE:
          this.$refs.opinionInput.inputtext = '同意'
          break
        case SignOpinionType.DISAGREE:
          this.$refs.opinionInput.inputtext = '不同意'
          break
        case SignOpinionType.ABSTAIN:
          this.$refs.opinionInput.inputtext = '弃权'
          break
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.dialog-content {
  margin-bottom: 0px;
  margin-top: 55px;
  position: relative;
}
.fly {
  position: absolute;
  width: 360px;
  left: 50%;
  margin-left: -180px;
  top: -35px;
  height: 21px;
}
.wrapper {
  width: 100%;
  min-height: 180px;
  max-height: 305px;
  overflow-y: auto;
  overflow-x: hidden;
}
.opinion-input {
  padding: 15px 32px;
  padding-top: 5px;
}
.vote-container {
  width: 100%;
  display: inline-flex;
  flex-flow: row wrap;
  align-items: center;
  align-content: stretch;
  justify-content: center;
  height: 180px;
  padding: 0 32px;

  /deep/ .el-button:hover{
    border-color:#2190E2 !important;
  }
}
.others-opinions-collapse {
  padding: 15px 32px;
  padding-top: 5px;
  border: transparent;
  /deep/ .el-collapse-item__header {
    height: 30px;
    font-size: 14px;
  }
  /deep/ .el-collapse-item__content {
    padding-bottom: 0px;
    font-size: 14px;
    max-height: 150px;
    overflow: auto;
  }
  /deep/ .el-row {
    border-bottom: 1px solid #EBEEF5;
    line-height: 35px;
    color: #606266;
    /deep/ .el-col {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
}
</style>



