<template>
  <el-dialog
    id="processTrace"
    custom-class="dialog-fullscreen"
    :visible.sync="visible"
    :fullscreen="options.fullscreen"
    :close-on-click-modal="false"
    width="1024px">
    <!-- 模态窗头部 -->
    <div slot="title">
      <div class="headers">
        <div>{{'查看流程轨迹：' + this.processModelName}}</div>
      </div>
    </div>
    <!-- 模态窗内容 -->
    <div class="processtrace-content" id="processtrace-content">
      <div class="trace_state_img" v-if="currentTaskStatus && currentTaskStatus !== Const.TASK_STATUS_RUNNING" :style="{'background': stateimg}"></div>
      <el-table
        :max-height="options.fullscreen ? tableHeight : '340px'"
        :height="options.fullscreen ? '' : '340px'"
        border
        size="medium"
        tooltip-effect="light"
        row-key="gid"
        :data="gridData"
        default-expand-all
        :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
        ref="traceTable">
        <el-table-column property="activityDefName" label="活动名称" width="90" show-overflow-tooltip></el-table-column>
        <el-table-column property="taskStatusName" label="任务状态" width="90" show-overflow-tooltip></el-table-column>
        <el-table-column property="userName" label="操作用户" width="90" show-overflow-tooltip></el-table-column>
        <el-table-column label="操作类型" width="90">
          <template slot-scope="scope">
            <el-tag
              v-if="scope.row.operationType !== null"
              size="medium"
              :type="getType(scope.row.operationType)"
              disable-transitions>{{scope.row.operationTypeName}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column property="opinion" label="操作意见" show-overflow-tooltip></el-table-column>
        <el-table-column label="完成时间" width="160">
          <template slot-scope="scope">
            {{utils.strToDate(scope.row.endTime, true, null, true)}}
          </template>
        </el-table-column>
        <el-table-column property="orgName" label="组织名称" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column property="groupName" label="操作角色" width="100" show-overflow-tooltip></el-table-column>
        <el-table-column property="operatorOrder" label="操作次序" width="78" align="center"></el-table-column>
      </el-table>
      <div class="reminder-wrapper"><reminder ref="reminder"></reminder></div>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="saveBtnClick" size="medium">确定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import ProcessTraceTable from './js/ProcessTraceTable.js'
export default ProcessTraceTable
</script>
<style lang="scss" scoped>
.headers {
  color: #428bca;
}
/deep/ .el-dialog.is-fullscreen.dialog-fullscreen {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
}
/deep/ .el-dialog.is-fullscreen.dialog-fullscreen .el-dialog__header {
  flex-grow: 0;
  flex-shrink: 0;
}
/deep/ .el-dialog.is-fullscreen.dialog-fullscreen .el-dialog__body {
  flex-grow: 2;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  overflow: auto;
}
/deep/ .el-dialog.is-fullscreen.dialog-fullscreen .el-dialog__footer {
  flex-grow: 0;
  flex-shrink: 0;
}
/deep/ .el-dialog__header {
  border-bottom: 1px solid #e5e5e5!important;
}
/deep/ .el-dialog__footer {
  border-top: 1px solid #e5e5e5!important;
}
/deep/ .el-table td, .el-table th {
  padding: 6px 0;
}
.processtrace-content {
  width: 100%;
  height: 100%;
}
.trace_state_img {
  z-index: 2;
  top: 8px;
  position: absolute;
  margin-left: 843px;
  transform: rotate(-15deg);
  width: 80px;
  height: 80px;
}
.header-row-style th{
  color: #333;
  font-weight:normal;
  background: #FAFAFA;
}
.reminder-wrapper {
  margin-top: 10px;
  margin-bottom: -20px;
}
</style>



