<template>
  <div class="bpm-grid" style="text-align:right;">
    <el-row type="flex" justify="space-between" class="inner-header">
      <el-row :span="14" class="inner-toolbar" v-if="searcher || this.$scopedSlots['toolbar']">
        <slot name="toolbar"></slot>
      </el-row>
      <el-col :span="10">
        <el-input
          class="inner-grid-searcher"
          v-if="searcher"
          :value="inputText"
          @input="onSearchTextChange"
          size="mini"
          @keyup.enter.native="() => {if (searchImmediate) btnClickHandler()}"
          placeholder="输入关键字进行过滤">
          <el-button v-if="!searchImmediate" slot="append" icon="el-icon-search" @click="btnClickHandler()"></el-button>
        </el-input>
      </el-col>
    </el-row>
    <el-row>
      <el-table
        :height="gridHeight"
        class="inner-grid"
        tooltip-effect="light"
        :data="_showData"
        highlight-current-row
        v-bind="$props"
        v-on="$listeners"
        ref="grid">
        <template>
          <el-table-column
            v-if="selection"
            width="34"
            align="center">
            <template #header="slotProps">
              <!-- 全选复选框 -->
              <el-checkbox
                :disabled="disableCheckBox"
                :indeterminate="isIndeterminate"
                :value="isSelectAll"
                onClick="event.cancelBubble = true"
                @change="handleSelectAllEvent"></el-checkbox>
            </template>
            <template #default="{ row }">
              <!-- 复选框 -->
              <el-checkbox
                :value="isChecked(row)"
                :disabled="disableCheckBox && isCheckBoxDisable(row)"
                onClick="event.cancelBubble = true"
                @change="val => handleSelectEvent(row, val)"></el-checkbox>
            </template>
          </el-table-column>
          <el-table-column
            v-if="index"
            type="index"
            width="34"
            align="center"
            ></el-table-column>
            <slot></slot>
        </template>
      </el-table>
    </el-row>
    <el-row class="inner-pagination" v-if='pageable'>
      <el-pagination
        :page-size="pageSize"
        :current-page="currentPage"
        :page-sizes="pageSizes"
        :pager-count="5"
        :total="_total"
        @size-change="paginationSizeChange"
        @current-change="paginationPageChange"
        layout="total, sizes, prev, pager, next, jumper">
      </el-pagination>
    </el-row>
  </div>
</template>
<script>
import { Table } from 'element-ui'
export default {
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    ...Table.props,
    data: {
      type: Array,
      required: true
    },
    /**
     * 如果是多选，则为选中数组
     * 如果是单选，则为选中行数据
     */
    value: {
      type: [Array, Object]
    },
    /**
     * 是否多选
     */
    selection: {
      type: Boolean,
      default: false
    },
    /**
     * 是否禁用多选
     */
    disableCheckBox: {
      type: Boolean,
      default: false
    },
    /**
     * 是否有索引
     */
    index: {
      type: Boolean,
      default: false
    },
    /**
     * 是否有搜索控件
     */
    searcher: {
      type: Boolean,
      default: true
    },
    /**
     * 是否分页
     */
    pageable: {
      type: Boolean,
      default: false
    },
    pageSizes: {
      type: Array,
      default: () => ([10, 20, 50, 100])
    },
    total: Number,
    /**
     * 是否远程
     */
    remote: {
      type: Boolean,
      default: false
    },
    /**
     * 是否输入马上搜索
     */
    searchImmediate: {
      type: Boolean,
      default: false
    },
    props: {
      type: Object,
      default: () => ({
        /**
         * 每行数据唯一标识
         */
        'row-key': 'id',
        /**
         * 每行数据是否禁用勾选[String, Function]
         */
        'checkbox-disabled': 'disabled',
        /**
         * 对哪些字段进行过滤
         */
        'filter-attributes': []
      })
    }
  },
  data () {
    return {
      /**
       * 分页器属性: 当前页大小
       */
      pageSize: this.pageSizes[0],
      /**
       * 分页器属性: 当前页码
       */
      currentPage: 1,
      /**
       * 正在输入的搜索文字
       */
      inputText: '',
      /**
       * 搜索文字
       */
      searchText: ''
    }
  },
  computed: {
    _total () {
      if (this.remote) {
        return this.total
      } else {
        return this.data.length
      }
    },
    _showData () {
      if (!this.remote) {
        const filteredData = this.data.filter(this.filterMethod)
        if (this.pageable) {
          const fromIndex = (this.currentPage - 1) * this.pageSize
          const toIndex = this.currentPage * this.pageSize
          const showData = filteredData.slice(fromIndex, toIndex)
          return showData
        }
        return filteredData
      } else {
        return this.data
      }
    },
    isChecked () {
      return row => {
        const key = row[this.props['row-key']]
        if (!key) return false
        return this.valueKeyList.indexOf(key) !== -1
      }
    },
    isCheckBoxDisabled () {
      return row => {
        const key = this.props['disabled'] || ''
        if (key instanceof String) {
          return row[key]
        } else if (key instanceof Function) {
          return key(row) || false
        }
      }
    },
    isSelectAll () {
      return this.currentShowKeyList
        .map(key => this.valueKeyList.indexOf(key) !== -1)
        .reduce((a, b) => a && b, true)
    },
    isIndeterminate () {
      return this.currentShowKeyList
        .map(key => this.valueKeyList.indexOf(key) !== -1)
        .reduce((a, b) => a || b, false) && !this.isSelectAll
    },
    currentShowKeyList () {
      return this._showData.map(row => row[this.props['row-key']])
    },
    valueKeyList () {
      if (this.value) return this.value.map(row => row[this.props['row-key']])
      else return []
    },
    filterMethod () {
      const attributes = this.props['filter-attributes'] || []
      return row => {
        if (attributes.length === 0) return true
        return attributes
          .map(key => {
            let str
            if (key instanceof Function) {
              str = key(row).toString().toLowerCase()
            } else {
              str = row[key]
            }
            if (!str) return false
            return str.indexOf(this.searchText.toString().toLowerCase()) !== -1
          })
          .reduce((x, y) => x || y, false)
      }
    },
    gridHeight () {
      const isToolbar = this.searcher || this.$scopedSlots['toolbar']
      const pageable = this.pageable
      return (this.height - (isToolbar ? 40 : 0) - (pageable ? 32 : 0)) + 'px'
    }
  },
  methods: {
    /**
     * 清空搜索框
     */
    clearSearcher () {
      this.inputText = ''
      this.searchText = ''
      if (this.remote) this.remoteConditionChangeEvent()
    },
    /**
     * 全选事件
     */
    handleSelectAllEvent (val) {
      this.selectAll = val
      const diff = this._showData.filter(row => {
        const currentKey = row[this.props['row-key']]
        const index = this.valueKeyList.indexOf(currentKey)
        const isAlreadyCheck = (index !== -1)
        return isAlreadyCheck !== val
      })
      let newValue = this.value.slice(0)
      if (val) {
        newValue.push(...diff)
      } else {
        const diffkeys = diff.map(row => row[this.props['row-key']])
        newValue = newValue.filter(row => diffkeys.indexOf(row[this.props['row-key']]) === -1)
      }
      this.$emit('change', newValue)
    },
    /**
     * 选中事件
     */
    handleSelectEvent (row, val) {
      this.updateValue(row, val)
    },
    updateValue (row, isAdd) {
      const currentKey = row[this.props['row-key']]
      const index = this.valueKeyList.indexOf(currentKey)
      const isAlreadyCheck = (index !== -1)
      if (isAlreadyCheck && !isAdd) {
        const newValue = this.value.slice(0, index)
        newValue.push(...this.value.slice(index + 1))
        this.$emit('change', newValue)
      } else if (!isAlreadyCheck && isAdd) {
        const newValue = this.value.slice(0)
        newValue.push(row)
        this.$emit('change', newValue)
      }
    },
    /**
     * 点击搜索按钮事件
     */
    btnClickHandler () {
      this.searchText = this.inputText
      if (this.remote) this.remoteConditionChangeEvent()
    },
    paginationSizeChange (size) {
      this.$emit('pagination-pagesize-change', size)
      if (this.remote) this.remoteConditionChangeEvent()
      this.pageSize = size
    },
    paginationPageChange (page) {
      this.$emit('pagiantion-page-change', page)
      if (this.remote) this.remoteConditionChangeEvent()
      this.currentPage = page
    },
    onSearchTextChange (newVal) {
      this.$emit('searchTextChange', newVal)
      this.inputText = newVal
      if (this.searchImmediate) {
        this.searchText = this.inputText
        if (this.remote) this.remoteConditionChangeEvent()
      }
    },
    remoteConditionChangeEvent () {
      this.$emit('remote-conditions-change', {
        page: this.currentPage,
        size: this.pageSize,
        searchText: this.searchText
      })
    },
    getCheckedRow () {
      return this.data.filter(this.isChecked)
    }
  },
  watch: {
    value (val) {
      // this.toggleSelections()
    }
  }
}
</script>
<style lang="scss">
.bpm-grid {
  width: 100%;
  height: 100%;
}
.bpm-grid .inner-header {
  height: 36px;
}
.bpm-grid .inner-pagination {
  height: 32px;
}
</style>



