import Constructor from './Constructor'
import RollbackConstructor from './RollbackConstructor'
import HelpConstructor from './HelpConstructor'
import ExtrapostConstructor from './ExtrapostConstructor'

export default async function (action, params) {
  if (['start', 'complete'].indexOf(action) !== -1) {
    return new Constructor(params)
  } else if (action === 'help') {
    return new HelpConstructor(params)
  } else if (action === 'extrapost') {
    return new ExtrapostConstructor(params)
  } else if (action === 'rollback') {
    return new RollbackConstructor(params)
  }
}



