import AbstractAction from './AbstractAction'

// import ecpService from 'ecp.service'
import ecpService from '../../utils/ProxyService'
import Const from '../Const'

class CompleteAction extends AbstractAction {
  constructor (options) {
    super(options, 'complete')
  }

  /**
   * 检查传入参数
   * @param {Object} options
   */
  checkOptions (options) {
    if (options.action !== 'complete') {
      throw new Error('action must be complete')
    }
    if (options.taskId == null || options.taskId === '') {
      throw new Error('taskId 为空')
    }
  }

  /**
   * 格式化数据
   * @param {Object} data
   */
  formatDate (data) {
    console.debug('---format data---')
    console.debug(data)
    let {nextActivities, currentUserTask, processInstId, processDefId} = data
    currentUserTask.processInstId = processInstId
    currentUserTask.processDefId = processDefId

    return {
      currentUserTask: currentUserTask,
      nextActivities: nextActivities
    }
  }

  /**
   * abstract created
   * @param {Object} instance
   */
  created (instance) {
    instance.visible = true
  }

  /**
   * 请求数据
   * @param {Object} options
   */
  async queryInfo (options) {
    let url = Const.URL_findCompleteNext
    let params = {
      bizDataModel: options.bizDataModel,
      taskVariables: options.taskVariables,
      taskId: options.taskId
    }
    let resp = await ecpService.doPost(url, params)
    return resp.data
  }

  /**
   * root store
   */
  getRootStore () {
    return {}
  }
}

export default CompleteAction



