import loginContextUtil from '../utils/LoginContextUtil'

const storeLastFinishedTask = function (params, result) {
  /* eslint-disable */
  if ($ && $.jStorage) {
    $.jStorage.set('taskId', params.taskId)
  }
  localStorage.setItem('taskId', params.taskId)
}

const autoOpenNextTask = async function (params, result) {
  console.debug(arguments)
  const { nextTasks } = result
  const loginContext = await loginContextUtil.getLoginContext()
  const currentUserId = loginContext.userId
  const urls = nextTasks
    .filter(task => task.userId === currentUserId)
    .filter(task => task.url)
    .map(task => task.url)
  urls.forEach(url => {
    setTimeout(function () {
      window.open('http://' + window.location.host + url, '_blank')
    }, 1)
  })
  if (urls.length > 0) {
    setTimeout(function () {
      window.open('about:blank', '_top')
      window.close()
      // 刷新代办消息
      /* eslint-disable */
      if ($ && $.jStorage) {
        $.jStorage.set('bpm.taskTodoTask.refresh', '发送消息成功' + new Date().getTime())
        $.jStorage.set('bpm.taskExecution.unRegist', '发送消息成功' + new Date().getTime())
      }
    }, 500)
  }
}
export default {
  'auto-open-next-task': autoOpenNextTask,
  'store-last-finished-task': storeLastFinishedTask
}



