<template>
  <bpmn-dialog
    :id="id + '-el-dialog'"
    :isPass="isPass"
    :visible="visible"
    :processInstId="currentUserTask.processInstId"
    @close="doClose"
    ref="dialog">
    <!-- 模态窗内容 -->
    <div class="dialog-content">
      <!-- 传递成功页面 -->
      <finish-page
        :passNames="nextAssignmentUserNames"
        :signCompleteState="signCompleteState"
        :mode="mode"
        v-if='isPass'>
      </finish-page>
      <!-- 传递页面 -->
      <div v-if="!isPass">
        <normal-page
          v-if="mode === 'normal'"
          :options="options"
          :currentUserTask="currentUserTask"
          :nextActivities="nextActivities"
          ref="page"></normal-page>
        <optional-page
          v-if="mode === 'option'"
          :options="options"
          :currentUserTask="currentUserTask"
          :nextActivities="nextActivities"
          :confirmBtnDisable.sync="confirmBtnDisable"
          ref="page"></optional-page>
        <sign-page
          v-if="mode === 'sign'"
          :options="options"
          :currentUserTask="currentUserTask"
          :nextActivities="nextActivities"
          ref="page"></sign-page>
        <help-confirm-page
          v-if="mode === 'help-confirm'"
          :options="options"
          :currentUserTask="currentUserTask"
          :nextActivities="nextActivities"
          ref="page"></help-confirm-page>
      </div>
      <!-- 自定义区域 -->
      <div id="extend"></div>
    </div>
    <!-- 模态窗脚部 -->
    <div slot="footer">
      <div class="buttons">
        <el-button
          size='medium'
          :disabled="confirmBtnDisable"
          @click.native='isPass? doClose() : action()'
          type="primary"
          ><i v-if="!isPass && ( mode === 'normal' || mode === 'option')"
            class="iconfont icon-chuandi"
            style="color:white;font-size:14px;"/>
          <i v-if="!isPass && mode === 'help-confirm'"
            class="iconfont icon-xieban"
            style="color:white;font-size:14px;"/>
          <i v-if="!isPass && mode === 'sign'"
            class="iconfont icon-huiqian"
            style="color:white;font-size:14px;"/>{{ buttontext }}</el-button>
      </div>
    </div>
  </bpmn-dialog>
</template>

<script>
import LoadingUtil from '../utils/LoadingUtil'
// import ErrorUtil from '../utils/ErrorUtil'
import deepClone from '../utils/deepClone'
// import deepMerge from '../utils/deepMerge'

import AbstractConstruactor from './mixins/AbstractConstruactor'

import BpmnDialog from '../components/BpmnDialog/BpmnDialog'
import NormalPage from '../pages/NormalPage'
import OptionalPage from '../pages/OptionalPage'
import SignPage from '../pages/SignPage'
import HelpConfirmPage from '../pages/HelpConfirmPage'
import FinishPage from '../components/FinishPage/FinishPage'

import ecpService from '../utils/ProxyService'
// import ErrorUtil from '../utils/ErrorUtil'
import Const from '../entrances/Const'
import TaskBizState from '../enumType/TaskBizState'
import CompletionState from '../enumType/CompletionState'
import SelectType from '../enumType/SelectType'

export default {
  mixins: [AbstractConstruactor],
  components: {
    NormalPage,
    OptionalPage,
    HelpConfirmPage,
    SignPage,
    FinishPage,
    BpmnDialog
  },
  data () {
    return {
      visible: false,
      isPass: false,
      nextAssignmentUserNames: [],
      confirmBtnDisable: false,
      LoadingUtil,
      mode: 'common',
      signOpinionType: null,
      signCompleteState: null
    }
  },
  props: {
    id: {
      type: String
    },
    options: {
      type: Object,
      require: true
    },
    nextActivities: {
      type: Array,
      require: true
    },
    currentUserTask: {
      type: Object,
      require: true
    }
  },
  async mounted () {
    window.instance = this
    await this.dispatch()
  },
  methods: {
    async dispatch () {
      let options = this.options
      let currentUserTask = this.currentUserTask
      let nextActivities = this.nextActivities
      let nextOptionalActivities = []

      if (currentUserTask.bizState === TaskBizState.HELP) {
        this.mode = 'help-confirm'
        nextActivities.map(activity => {
          activity.forbidUserSelect = true
          activity.participant.participantDef = {}
          activity.participant.participantDef.selectType = SelectType.SELECT_USER_TYPE
          return activity
        })
        return
      }
      if (currentUserTask.signActivity && this.signOpinionType == null) {
        this.mode = 'sign'
        return
      }

      for (let activity of nextActivities) {
        if (activity.optionalActivity && activity.optionalActivity === true) {
          nextOptionalActivities.push(activity)
        }
      }

      if (nextOptionalActivities.length > 0) {
        if (nextOptionalActivities.length !== nextActivities.length) {
          console.error('data error: all activities must be "optionalActivity: true"')
        }
        await this.$_preloadOptionalActivities(nextOptionalActivities, options)
        this.nextActivities = nextOptionalActivities
        this.mode = 'option'
        return
      }
      nextActivities = this.$_flatSignActivities(nextActivities)
      this.nextActivities = nextActivities
      this.mode = 'normal'
    },
    async $_preloadOptionalActivities (nextOptionalActivities, options) {
      if (!options.taskId && options.action === 'start') {
        this.doClose()
        throw new Error('暂不支持首环节后直接连接自选网关使用start模式')
      }
      for (let index in nextOptionalActivities) {
        let activity = nextOptionalActivities[index]
        if (activity.needFindNext === true) {
          let params = {
            taskId: options.taskId,
            bizDataModel: options.bizDataModel,
            taskVariables: options.taskVariables,
            selectedOptionalId: activity.activityDefId,
            optionalFindNext: true,
            signOpinionType: 0
          }

          // 查询自选下环节活动
          console.debug('---preloadOptionalActivities---')
          let resp = await ecpService.doPost(Const.URL_findCompleteNext, params)
          let nextActivities = resp.data.nextActivities
          nextActivities = this.$_flatSignActivities(nextActivities)
          nextOptionalActivities[index].activities = nextActivities
        } else {
          nextOptionalActivities[index].activities = this.$_flatSignActivities([].concat(nextOptionalActivities[index]))
        }
      }
    },
    $_flatSignActivities (nextActivities) {
      return nextActivities.flatMap(activity => {
        if (activity.signActivity && activity.signActivity === true) {
          let atv = deepClone(activity)
          delete atv.participants
          return activity.participants.map(participant => {
            let flatActivity = deepClone(atv)
            flatActivity.participant = participant
            return flatActivity
          })
        } else {
          return activity
        }
      })
    },
    async prepareParams () {
      if (this.mode === 'sign') {
        let { params, signCompleteState, newActivities } = await this.$refs.page.getParams()
        // 会签满足可以传递，转为普通传递
        if (signCompleteState === CompletionState.ACHIEVE_PASS) {
          // 保存一下signOpinionType 再下次准备参数时加入
          this.signOpinionType = params.signOpinionType
          this.nextActivities = newActivities
          await this.dispatch()
          // 把审批意见带到下环节
          this.$nextTick(() => {
            this.$refs.page.defaultOpinion = params.opinion
          })
          return { stopPassing: true }
        }
        this.signCompleteState = signCompleteState
        params.nextTaskAssignments = []
        return { params }
      } else {
        let { params } = await this.$refs.page.getParams()
        // 会签参与者场景没有选择参与者
        if (params.nextTaskAssignments.length === 0 && this.nextActivities.length !== 0) {
          throw new Error('请至少选择一个参与者。')
        }
        // 如果是由会签转为普通传递 取出之前的signOpinionType加入传入参数
        if (this.signOpinionType != null) {
          params.signOpinionType = this.signOpinionType
          params.opinion = opinionTypeConvert(params.signOpinionType) + '：' + params.opinion
        }
        return { params }
      }
    }
  },
  computed: {
    buttontext () {
      return this.isPass ? '确认' : (
        this.mode === 'sign' ? '会签' : (
          this.mode === 'help-confirm' ? '协办确认'
            : '传递'))
    }
  }
}

function opinionTypeConvert (opinionType) {
  switch (opinionType) {
    case 0: return '同意'
    case 1: return '不同意'
    case 2: return '弃权'
  }
}
</script>

<style lang="scss" scoped>
.dialog-content {
  margin-bottom: 0px;
  margin-top: 55px;
  position: relative;
}
.fly {
  position: absolute;
  width: 360px;
  left: 50%;
  margin-left: -180px;
  top: -35px;
  height: 21px;
}
.wrapper {
  width: 100%;
  min-height: 180px;
  max-height: 305px;
  overflow-y: auto;
  overflow-x: hidden;
}
.opinion-input {
  padding: 15px 32px;
  padding-top: 5px;
}
.vote-container {
  width: 100%;
  display: inline-flex;
  flex-flow: row wrap;
  align-items: center;
  align-content: stretch;
  justify-content: center;
  height: 180px;
  padding: 0 32px;

  /deep/ .el-button:hover{
    border-color:#2190E2 !important;
  }
}
</style>



