import ecpService from '../../../../utils/ProxyService'
import utils from 'ecp.utils'

import Const from './Const'
import OperationType from '../../../../enumType/OperationType'

import Reminder from '../Reminder.vue'
import ErrorUtil from '../../../../utils/ErrorUtil'

function findChildrenFrom (origin) {
  return this.map(each => {
    let children = origin.filter(c => c.pid === each.activityDefId)
    if (children.length > 0) {
      children = findChildrenFrom.call(children, origin)
      each.children = children
    }
    return each
  })
}

function S4 () {
  return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1)
}

export default {
  components: {
    Reminder
  },
  data () {
    return {
      Const: Const,
      utils: utils,
      options: undefined,
      visible: false,
      gridData: [],
      /**
       * 状态图片url
       */
      stateimg: '',
      currentTaskStatus: undefined,
      processModelName: '',
      tableHeight: '390px'
    }
  },
  mounted () {
    window.processTrace = this

    this.queryTraceData(this.options, (data) => {
      this.$store.commit('setProcessInstAndDefIdSetted', {
        processInstId: data.taskTraces[0].processInstId,
        processDefId: data.taskTraces[0].processDefId
      })

      if (data != null) {
        var traceList = data.taskTraces
        traceList.forEach((e, i, a) => {
          if (e.gid === '') a[i].gid = S4()
        })
        this.processModelName = data.processInstance.processDisplayName || ''
        this.currentTaskStatus = data.processInstance.status
        if (this.currentTaskStatus !== Const.TASK_STATUS_RUNNING) {
          if (this.currentTaskStatus === Const.TASK_STATUS_SUSPENDED) {
            this.stateimg = Const.SUSPENDED_IMG_URL
          }
          if (this.currentTaskStatus === Const.TASK_STATUS_COMPLETE) {
            this.stateimg = Const.COMPLETE_IMG_URL
          }
          if (this.currentTaskStatus === Const.TASK_STATUS_TERMINATED) {
            this.stateimg = Const.TERMINATED_IMG_URL
          }
        }

        let roots = traceList.filter(e => e.pid === '' || e.pid === undefined)

        let newList = findChildrenFrom.call(roots, traceList)

        // 数据绑定到表格
        this.gridData = newList
      }
    }, this.options.filterTerminateData)
  },
  watch: {
    visible (val) {
      if (val && this.options.fullscreen) {
        this.$nextTick(() => {
          this.tableHeight = document.querySelector('.processtrace-content').offsetHeight
        })
      }
    }
  },
  methods: {
    async saveBtnClick () {
      try {
        if (this.$refs.reminder !== undefined) {
          await this.$refs.reminder.saveReminder()
        }
        this.visible = false
      } catch (e) {
        ErrorUtil.alertError(e)
      }
    },
    /**
     * 查询流程轨迹数据.
     */
    queryTraceData (options, callback, filterTerminateData) {
      let taskId = options.taskId
      let processInstId = options.processInstId
      var url = 'necp/mapp/bpm-engine/query/taskTraces/tracesOfInstance?' +
        `filterTerminateData=${filterTerminateData}` +
        (taskId ? '&taskId=' + taskId : '') +
        (processInstId ? '&processInstId=' + processInstId : '')
      ecpService.doGet(url)
        .then(resp => {
          if (resp.headers && resp.headers.error_code) throw resp.data.errMsg || '未知错误'
          callback && callback(resp.data)
        }).catch(e => {
          console.error(e)
        })
    },
    getType (operationType) {
      switch (operationType) {
        case OperationType.POST: return 'success'
        case OperationType.ROLLBACK:
        case OperationType.TERMINATE:
        case OperationType.DRAWBACK: return 'danger'
      }
    }
  }
}



