import deepMerge from '../../utils/deepMerge'
import StringUtil from '../../utils/StringUtil'

import Options from '../../vo/options'

import dispatch from '../../construactors/dispatch'
import LoginContextPromise from '../../utils/LoginContextPromise'
import Vuex from 'vuex'

class AbstractAction {
  constructor (options, id) {
    let defaultOptions = new Options()
    this.options = deepMerge(defaultOptions, options)
    this.id = id
  }

  async init () {
    // check
    this._basicCheck()
    this.checkOptions(this.options)

    // query info
    let data
    data = await this.queryInfo(this.options)
    this.rawData = data

    // format data
    let {currentUserTask, nextActivities} = this.formatDate(data)

    // init
    // get el
    this.el = this._getDOMElement(this.id)
    this._instance = await this._initInstance(currentUserTask, nextActivities)

    this._instance.$nextTick(async () => {
      await this.created(this._instance)
      this._afterRenderEvent()
    })
  }

  _afterRenderEvent () {
    let afterRender = this.options.afterRender
    if (afterRender && afterRender instanceof Function) {
      afterRender({
        container: this._getExtendDOMElement(),
        data: this.rawData
      })
    }
  }

  _getDOMElement (id) {
    let el = document.getElementById(id + '-el-dialog')
    if (!el) {
      el = document.createElement('div')
      document.body.appendChild(el)
    }
    return el
  }

  async _initInstance (currentUserTask, nextActivities) {
    let instance = await dispatch(this.options.action, {
      el: this.el,
      store: this._mergeStore(),
      propsData: {
        id: this.id,
        options: this.options,
        currentUserTask: currentUserTask,
        nextActivities: nextActivities
      }
    })
    return instance
  }

  _basicCheck () {
    if (StringUtil.isEmpty(this.id)) {
      throw new Error('error type id or id is empty')
    }
  }

  _getExtendDOMElement () {
    return document.getElementById('extend')
  }

  /**
   * store
   */
  _mergeStore () {
    let store = deepMerge({
      state: {
        loginContextPromise: new LoginContextPromise(),
        rawData: this.rawData
      },
      actions: {
        whenLoginContextSetted ({ state }, cb) {
          state.loginContextPromise.then(cb)
        }
      }
    }, this.getRootStore() || {})
    return new Vuex.Store(store)
  }

  /**
   * root store
   */
  getRootStore () {
    return {}
  }

  /**
   * abstract function
   * @param {Object} options
   */
  checkOptions (options) {
    throw new Error('undefined checkOptions')
  }

  /**
   * abstract function
   * @param {Object} options
   */
  queryInfo (options) {
    throw new Error('undefined queryInfo')
  }

  /**
   * abstract function
   * @param {Object} data
   */
  formatDate (data) {
    throw new Error('undefined formatDate')
  }

  /**
   * abstract created
   * @param {Object} instance
   */
  created (instance) {
    throw new Error('undefined created')
  }
}

export default AbstractAction



