<template>
  <bpmn-dialog
    :id="id + '-el-dialog'"
    :isPass="isPass"
    :visible="visible"
    :processInstId="currentUserTask.processInstId"
    @close="doClose"
    ref="dialog">
    <!-- 模态窗内容 -->
    <div class="dialog-content">
      <!-- 传递成功页面 -->
      <finish-page
        :passNames="nextAssignmentUserNames"
        mode="help"
        v-if='isPass'>
      </finish-page>
      <!-- 传递页面 -->
      <div v-if="!isPass">
        <help-page
          :options="options"
          :currentUserTask="currentUserTask"
          :nextActivities="nextActivities"
          ref="page"></help-page>
      </div>
      <!-- 自定义区域 -->
      <div id="extend"></div>
    </div>
    <!-- 模态窗脚部 -->
    <div slot="footer">
      <div class="buttons">
        <el-button
          size='medium'
          :disabled="confirmBtnDisable"
          @click.native='isPass? doClose() : action()'
          type="primary"
          ><i class="iconfont icon-xieban" style="color:white;font-size:14px;"></i>{{ buttontext }}</el-button>
      </div>
    </div>
  </bpmn-dialog>
</template>

<script>
import AbstractConstruactor from './mixins/AbstractConstruactor'

import LoadingUtil from '../utils/LoadingUtil'

import BpmnDialog from '../components/BpmnDialog/BpmnDialog'
import HelpPage from '../pages/HelpPage'
import FinishPage from '../components/FinishPage/FinishPage'

export default {
  mixins: [AbstractConstruactor],
  components: {
    HelpPage,
    FinishPage,
    BpmnDialog
  },
  data () {
    return {
      visible: false,
      isPass: false,
      nextAssignmentUserNames: [],
      confirmBtnDisable: false,
      LoadingUtil,
      signOpinionType: null
    }
  },
  props: {
    id: {
      type: String
    },
    options: {
      type: Object,
      require: true
    },
    nextActivities: {
      type: Array,
      require: true
    },
    currentUserTask: {
      type: Object,
      require: true
    }
  },
  async mounted () {
    window.instance = this
  },
  methods: {
    async prepareParams () {
      let { params } = await this.$refs.page.getParams()
      return { params }
    }
  },
  computed: {
    buttontext () {
      return this.isPass ? '完成' : '协办'
    }
  }
}
</script>

<style lang="scss" scoped>
.dialog-content {
  margin-bottom: 0px;
  margin-top: 55px;
  position: relative;
}
.fly {
  position: absolute;
  width: 360px;
  left: 50%;
  margin-left: -180px;
  top: -35px;
  height: 21px;
}
.wrapper {
  width: 100%;
  min-height: 180px;
  max-height: 305px;
  overflow-y: auto;
  overflow-x: hidden;
}
.opinion-input {
  padding: 15px 32px;
  padding-top: 5px;
}
.vote-container {
  width: 100%;
  display: inline-flex;
  flex-flow: row wrap;
  align-items: center;
  align-content: stretch;
  justify-content: center;
  height: 180px;
  padding: 0 32px;

  /deep/ .el-button:hover{
    border-color:#2190E2 !important;
  }
}
</style>



