import { Loading } from 'element-ui'

class Freezer {
  constructor (id, text) {
    const options = {
      lock: true,
      text: text || '加载中',
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 0.7)'
    }
    if (id instanceof String || id instanceof Node) {
      this.freezer = Loading.service({
        target: id,
        ...options
      })
    } else {
      this.freezer = Loading.service({
        fullscreen: true,
        ...options
      })
    }
  }

  unfreeze () {
    this.freezer.close()
  }
}

export default {
  freeze (domId, text) {
    return new Freezer(domId, text)
  }
}



