import EcpService from '../../../utils/ProxyService'
export default {
  async getOrgs () {
    let resp = await EcpService.doPost(`necp/mapp/bpm-engine/participant/orgs`)
    return resp.data
  },
  async getUsers (orgId, page, size, searchText) {
    let resp = await EcpService.doPost(`necp/mapp/bpm-engine/participant/users`, {
      orgId: orgId,
      size: 999
    })
    return resp.data
  },
  async getIsCC (processDefId, activityDefId) {
    let resp = await EcpService.doGet(`necp/mapp/bpm-engine/query/processDefinitions/processDefId/${processDefId}/flowElements/${activityDefId}/fixedProperties`)
    return resp.data.find(v => v.id === 'permitTaskCc').value
  },
  async getSelectedUsers (processDefId, activityDefId) {
    let resp = await EcpService.doGet(`necp/mapp/bpm-engine/query/processDefinitions/processDefId/${processDefId}/flowElements/${activityDefId}/carbonCopyUserIds`)
    return resp.data
  }
}



