import ecpService from './ProxyService'

let url = 'necp/mapp/bpm-engine/context'

export default {
  async getLoginContext () {
    if (window.ecp_login_context == null) await this._init()
    return window.ecp_login_context
  },
  async _init () {
    try {
      const resp = await ecpService.doPost(url)
      if (!resp || !resp.data) throw new Error('连接超时，请重新登陆')
      if (resp.data) window.ecp_login_context = resp.data
    } catch (e) {
      throw e
    }
  }
}



