<template>
  <div :style="style(nextActivities.length)" class="multi-assignments-container">
    <!-- participant渲染 -->
    <participant
      class="participant"
      :cardStyle="nextActivities.length === 1 ? 'single' : 'multi'"
      v-for="(activity, index) in nextCommonActivities"
      :key="index + '_' + activity.activityDefId"
      :activity="activity"
      :options="options"
      :forbidUserSelect="activity.forbidUserSelect"
      :current-user-task="currentUserTask"
      ref="participant"
    ></participant>
    <!-- sign-participant渲染 -->
    <sign-participant
      class="participant"
      :cardStyle="nextActivities.length === 1 ? 'single' : 'multi'"
      v-for="(activity, index) in nextSignActivities"
      :key="index + '_' + activity.activityDefId"
      :activity="activity"
      :options="options"
      :current-user-task="currentUserTask"
      ref="participant"
    ></sign-participant>
    <!-- 末任务传递 -->
    <div class="last-task-label" v-if="nextActivities.length === 0">
      <last-task ref="lastTask"></last-task>
    </div>
  </div>
</template>
<script>
import Participant from '../components/Participant/CommonParticipant'
import SignParticipant from '../components/Participant/SignParticipant'
export default {
  components: {
    Participant,
    SignParticipant
  },
  data () {
    return {}
  },
  mounted () {
    window.container = this
  },
  props: {
    options: {
      type: Object,
      require: true
    },
    nextActivities: {
      type: Array,
      require: true
    },
    currentUserTask: {
      type: Object,
      require: true
    }
  },
  methods: {
    getNextTaskAssignments () {
      let nextTaskAssignments = []
      let participants = this.$refs.participant
      if (participants instanceof Array) {
        participants.forEach(e => {
          nextTaskAssignments = nextTaskAssignments.concat(e.getAssignment())
        })
      } else {
        nextTaskAssignments = nextTaskAssignments.concat(participants.getAssignment())
      }
      return nextTaskAssignments
    },
    style (length) {
      if (length === 1 || length === 0) {
        return {
          'justify-content': 'center'
        }
      } else if (length > 1) {
        return {
          'justify-content': 'space-between'
        }
      }
    }
  },
  computed: {
    nextCommonActivities () {
      return this.nextActivities.filter(activity => !activity.signActivity)
    },
    nextSignActivities () {
      return this.nextActivities.filter(activity => activity.signActivity)
    }
  }
}
</script>
<style lang="scss" scoped>
.multi-assignments-container {
  width: 680px;
  display: inline-flex;
  flex-flow: row wrap;
  align-items: center;
  align-content: stretch;
  padding: 0 32px;
}
/deep/ .participant {
  flex: none;
  margin-top: 8px;
  margin-bottom: 8px;
}

</style>



