<template>
  <!-- 模态窗内容 -->
  <div class="dialog-content">
    <!-- 回退环节 -->
    <activity-select
      style="font-size:12px;position:absolute;left:32px;top:-35px;color:#999999;"
      :options="nextActivities"
      :selectValue.sync="selectedOption"
      text="回退环节"
    ></activity-select>
    <!-- 背景图 -->
    <div class="rollback-svg"><rollback-svg></rollback-svg></div>
    <!-- 抄送 -->
    <div class="cc"><carbon-copy ref="carboncopy"></carbon-copy></div>
    <!-- 当前环节 -->
    <div
      style="font-size:12px;position:absolute;right:32px;top:-35px;text-align:right;color:#999999;"
      >当前环节<br><div class="ellipsis" style="font-size:13px;width:100px;">{{ currentUserTask.activityDefName }}</div>
      </div>
    <div name="pass-content">
      <div v-if="selectedOption !== '' && selectedOption != null">
        <div class="wrapper" v-if="selectedActivities.length > 0">
          <participants-container
            :options="options"
            :currentUserTask="currentUserTask"
            :nextActivities="selectedActivities"
            ref="container"
          ></participants-container>
        </div>
        <div class="wrapper" v-if="selectedActivities.length === 0">
          <last-task />
        </div>
      </div>
      <div class="wrapper" v-if="selectedOption === '' || selectedOption == null"> </div>
      <div class="opinion-input">
        <opinion-input
          :operationType="OperationType.ROLLBACK"
          :placeholder="'审批不通过'"
          :defaultOpinion="defaultOpinion"
          ref="opinionInput"
        ></opinion-input>
      </div>
    </div>
  </div>
</template>

<script>
import AbstractPage from './mixins/AbstractPage'

import ParticipantsContainer from '../containers/ParticipantsContainer'
import RollbackSvg from '../components/svg/Rollback'
import LastTask from '../components/LastTask/LastTask'
import LastTaskSvg from '../components/svg/LastTask'
import OpinionInput from '../components/OpinionInput/OpinionInput'
import FinishPage from '../components/FinishPage/FinishPage'
import ActivitySelect from '../components/ActivitySelect/ActivitySelect'
import CarbonCopy from '../components/CarbonCopy/pc/CarbonCopy'

import TaskBizState from '../enumType/TaskBizState'
import OperationType from '../enumType/OperationType'

import ecpService from '../utils/ProxyService'
import Const from './Const'
import ErrorUtil from '../utils/ErrorUtil'

export default {
  mixins: [AbstractPage],
  components: {
    ParticipantsContainer,
    RollbackSvg,
    OpinionInput,
    LastTask,
    LastTaskSvg,
    FinishPage,
    ActivitySelect,
    CarbonCopy
  },
  data () {
    return {
      OperationType,
      TaskBizState,
      defaultOpinion: '',
      selectedOption: ''
    }
  },
  props: {
  },
  mounted () {
    window.page = this
    this.selectedOption = this.nextActivities[0]
    this.$emit('update:confirmBtnDisable', true)
  },
  methods: {
    async getParams () {
      console.debug('---get params---')
      let { assignments, opinion, carbonCopyUserIds } = await this.$_assignmentsAndOpinion()
      console.debug(assignments, opinion, carbonCopyUserIds)
      if (this.selectedOption == null || this.selectedOption === '') {
        ErrorUtil.popError(new Error('请选择下环节'))
        return {
          stopPassing: true
        }
      }
      let params = {
        businessKey: this.options.businessKey,
        bizDataModel: this.options.bizDataModel,
        taskVariables: this.options.taskVariables,
        taskId: this.options.taskId,
        nextTaskAssignments: assignments,
        nextTaskAssignment: assignments[0],
        opinion: opinion,
        carbonCopyUserIds: carbonCopyUserIds
      }

      return {
        params: params
      }
    },
    async sendParams (params) {
      delete params.nextTaskAssignments
      let url = Const.URL_rollback
      console.debug('---sending params---')
      console.debug(params)
      let resp = await ecpService.doPost(url, params)
      console.debug(resp.data)
      return resp.data
    }
  },
  computed: {
    selectedActivities () {
      return [ this.selectedOption ]
    }
  },
  watch: {
    selectedOption (val) {
      if (val === '' || val == null) {
        this.$emit('update:confirmBtnDisable', true)
      } else {
        this.$emit('update:confirmBtnDisable', false)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.dialog-content {
  margin-bottom: 0px;
  margin-top: 55px;
  position: relative;
}
.rollback-svg {
  position: absolute;
  width: 360px;
  left: 50%;
  margin-left: -180px;
  top: -35px;
  height: 21px;
}
.wrapper {
  width: 100%;
  min-height: 180px;
  max-height: 305px;
  overflow-y: auto;
  overflow-x: hidden;
}
.opinion-input {
  padding: 15px 32px;
  padding-top: 5px;
}
.cc {
  position: absolute;
  left: 50%;
  width: 100px;
  margin-left: -50px;
  top: -25px;
}
</style>



