import LoginContextUtil from '../utils/LoginContextUtil'
import ErrorUtil from '../utils/ErrorUtil'
class LoginContextPromise {
  constructor () {
    this._promise = new Promise((resolve, reject) => {
      LoginContextUtil.getLoginContext().then(loginContext => {
        resolve(loginContext)
      }).catch(e => {
        ErrorUtil.alertError(e)
      })
    })
  }

  then (method) {
    this._promise.then(loginContext => {
      method(loginContext)
      return loginContext
    })
    return this
  }
}
export default LoginContextPromise



