import reminder from '../../api/reminder'
import ModelState from '../ModelState'
import deepClone from '../../../../utils/deepClone'
import 'ecp.ext'

const state = {
  originData: null,
  gid: null,
  processInstId: null,
  processDefId: null,
  userId: null,
  messageType: 0,
  reminderItems: [],
  modelState: ModelState.DEFAULT,
  activityIdNameMap: {}
}

// getters
const getters = {
  currentData: (state, getters) => {
    return {
      gid: state.gid,
      processInstId: state.processInstId,
      userId: state.userId,
      messageType: state.messageType,
      reminderItems: state.reminderItems,
      modelState: state.modelState
    }
  }
}

// actions
const actions = {
  async saveReminder ({ state, getters, dispatch }) {
    let currentData = deepClone(getters.currentData)
    _diff(currentData, state.originData)
    let data = await reminder.saveReminder(currentData)
    if (!data) {
      throw new Error('保存环节订阅失败。')
    }
  },
  async getReminder ({ state, commit }) {
    if (state.processInstId == null ||
      state.processInstId.length === 0 ||
      state.userId == null ||
      state.userId.length === 0) {
      throw new Error('数据错误：no processInstId or userId。')
    }
    let data = await reminder.getReminder(state.processInstId, state.userId)
    data.reminderItems = data.reminderItems || []
    data.messageType = data.messageType || 0
    commit('setOriginData', data)
  },
  async getActivityIdNameMap ({ state, commit }) {
    if (state.processDefId == null ||
      state.processDefId.length === 0) {
      throw new Error('数据错误：no processDefId。')
    }
    let data = await reminder.getActivityIdNameMap(state.processDefId)
    let map = {}
    data.forEach(v => {
      map[v.id] = v.name
    })
    commit('setActivityIdNameMap', map)
  }
}

function _diff (currentData, originData) {
  // 订阅设置是否有修改
  if (currentData.messageType !== originData.messageType) {
    // 新增订阅
    if (originData && originData.gid.length === 0) {
      currentData.modelState = ModelState.NEW
    } else {
      // 修改订阅
      currentData.modelState = ModelState.MODIFY
    }
  }
  let currentItemMap = {}
  currentData.reminderItems.forEach(item => {
    currentItemMap[item.activityDefId] = item
  })
  let originItemMap = {}
  originData.reminderItems.forEach(item => {
    originItemMap[item.activityDefId] = item
  })
  let isMod = false
  // diff 查新增
  currentData.reminderItems.forEach(item => {
    let originItem = originItemMap[item.activityDefId]
    if (!originItem) {
      item.modelState = ModelState.NEW
      item.gid = Math.uuid()
      isMod = true
    }
  })
  // diff 查删除
  let deleteItems = []
  originData.reminderItems.forEach(item => {
    let currentItem = currentItemMap[item.activityDefId]
    if (!currentItem) {
      item.modelState = ModelState.DELETE
      deleteItems.push(item)
      isMod = true
    } else {
      currentItem.gid = item.gid
    }
  })
  currentData.reminderItems.push(...deleteItems)
  // 明细加入reminderId
  currentData.reminderItems.forEach(item => {
    item.reminderId = currentData.gid
  })
  if (isMod) currentData.modelState = ModelState.MODIFY
}

// mutations
const mutations = {
  setOriginData (state, originData) {
    state.originData = originData

    // 是否空记录
    if (originData.gid == null || originData.gid.length === 0) {
      // todo uuid赋值
      state.gid = Math.uuid()
    } else {
      state.gid = originData.gid
      state.processInstId = originData.processInstId
      state.messageType = originData.messageType
      state.reminderItems = originData.reminderItems
    }
  },
  setGid (state, gid) {
    state.gid = gid
  },
  setProcessInstId (state, processInstId) {
    state.processInstId = processInstId
  },
  setProcessDefId (state, processDefId) {
    state.processDefId = processDefId
  },
  setUserId (state, userId) {
    state.userId = userId
  },
  setMessageType (state, messageType) {
    state.messageType = messageType
  },
  setReminderItems (state, reminderItems) {
    state.reminderItems = reminderItems
  },
  setActivityIdNameMap (state, activityIdNameMap) {
    state.activityIdNameMap = activityIdNameMap
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}



