const deepMerge = (target, source) => {
  for (let key in source) {
    if (typeof source[key] === 'undefined') continue
    if (!(key in target) || target[key] == null) target[key] = {}
    target[key] = source[key] && source[key].toString() === '[object Object]'
      ? deepMerge(target[key], source[key]) : source[key]
  }
  return target
}

export default deepMerge



