import ecpService from 'ecp.service'
import { prefixUrl } from './UrlUtil'
import ErrorUtil from './ErrorUtil'

export default {
  // doPost (url, params, b, c) {
  //   return ecpService.doPost(prefixUrl(url), params, b, c)
  // },
  // doGet (url, a, b, c) {
  //   return ecpService.doGet(prefixUrl(url), a, b, c)
  // }
  async doPost (url, params, b, c) {
    let resp = await ecpService.doPost(prefixUrl(url), params, b, c)
    ErrorUtil.checkResponseError(resp)
    return resp
  },
  async doGet (url, a, b, c) {
    let resp = await ecpService.doGet(prefixUrl(url), a, b, c)
    ErrorUtil.checkResponseError(resp)
    return resp
  }
}



