import afterConfirmExtendEvents from './afterConfirmExtendEvents'
// import deepClone from '../utils/deepClone'
const map = {
  ...afterConfirmExtendEvents
}

export default {
  register (name) {
    return map[name](...Array.from(arguments).slice(1))
  },
  'events': map
}



