<template>
    <!-- 画布内容 -->
    <div :id="containerId + 'Canvas'" style="position:relative;width:100%;height:100%;">
      <!-- slider -->
      <div class="slider" style="position:absolute;left:10px;top:15px;z-index:2;">
        <el-slider
          :min="0.2"
          :max="4"
          :step="0.1"
          v-model="value"
          vertical
          @input="inputSlider"
          height="200px">
        </el-slider>
      </div>
    </div>
</template>
<script>
import ProcessGraphCanvas from './js/processGraphCanvas.js'
export default ProcessGraphCanvas
</script>



