export default {
  URL_findAllUserOpinion: 'necp/mapp/bpm-engine/service/userOpinion/findAll',

  URL_createUserOpinion: 'necp/mapp/bpm-engine/service/userOpinion/create',

  URL_deleteUserOpinion (gid) {
    return 'necp/mapp/bpm-engine/service/userOpinion/' + gid + '/delete'
  },

  URL_updateDefaultUserOpinion (gid) {
    return 'necp/mapp/bpm-engine/service/userOpinion/' + gid + '/updateDefault'
  },

  URL_serDefaultUserOpinion: 'necp/mapp/bpm-engine/service/userOpinion/setDefalut'
}



