export default {
  setHistorySelectedUsers ({processDefId = '', activityDefId = '', orgId = 'ALL', groupId = 'ALL', participantDefId = ''}, users) {
    if (!(users instanceof Array)) {
      console.error(new Error('data error format'))
    }
    window.localStorage.setItem(_encodeKey(processDefId, activityDefId, orgId, groupId, participantDefId), JSON.stringify(users))
  },
  getHistorySelectedUsers ({processDefId = '', activityDefId = '', orgId = 'ALL', groupId = 'ALL', participantDefId = ''}) {
    const usersJson = window.localStorage.getItem(_encodeKey(processDefId, activityDefId, orgId, groupId, participantDefId))
    if (usersJson) {
      return JSON.parse(usersJson)
    } else {
      return []
    }
  }
}

function _encodeKey (processDefId, activityDefId, orgId, groupId, participantDefId) {
  let _activityDefId
  if (activityDefId.indexOf('UserTask_') !== -1) {
    _activityDefId = activityDefId.slice(9)
  } else if (activityDefId.indexOf('ecpUserTask') !== -1) {
    _activityDefId = activityDefId.slice(11)
  } else {
    _activityDefId = activityDefId
  }
  const _participantDefId = participantDefId.slice(2)
  return `${processDefId}-${_activityDefId}_${orgId}_${groupId}_${_participantDefId}`
}



