export default {
  /**
   * 传递类型.
   */
  OPERATOR_POST_TYPE: 1,

  /**
   * 待处理类型.
   */
  OPERATOR_NOTODO_TYPE: 0,

  /**
   * 任务运行状态.
   */
  TASK_STATUS_RUNNING: 0,

  /**
   * 任务挂起状态.
   */
  TASK_STATUS_SUSPENDED: 1,

  /**
   * 任务完成状态.
   */
  TASK_STATUS_COMPLETE: 2,

  /**
   * 任务完成状态.
   */
  TASK_STATUS_TERMINATED: 3,
  /**
   * 任务完成图片地址.
   */
  COMPLETE_IMG_URL: 'url(/assets/components/bpm/runtime/images/img/succ.png)',

  /**
   * 任务挂起图片地址.
   */
  SUSPENDED_IMG_URL: 'url(/assets/components/bpm/runtime/images/img/guaqi.png)',
  // 任务终止图片地址
  TERMINATED_IMG_URL: 'url(/assets/components/bpm/runtime/images/img/terminated.png)'
}



