import taskExecute from './entrances/taskExecute.js'

import start from './entrances/start.js'
import complete from './entrances/complete.js'
import rollback from './entrances/rollback.js'
import help from './entrances/help.js'
import extrapost from './entrances/extrapost.js'

import processTrace from './entrances/processTrace'
import processGraph from './entrances/processGraph'

import ExtendEventFactory from './extend/extendEventFactory'

import './assets/font/iconfont-pass.css'
import './assets/css/bpmn.css'

const install = Vue => {
  Vue.prototype.$taskExecute = taskExecute
  Vue.prototype.$processTrace = processTrace
  Vue.prototype.$processGraph = processGraph
}

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  install,
  taskExecute,
  start,
  complete,
  rollback,
  help,
  extrapost,
  processTrace,
  processGraph,
  ExtendEventFactory
}



