import LoadingUtil from '../../utils/LoadingUtil'
// import ErrorUtil from '../../utils/ErrorUtil'
// import deepClone from '../../utils/deepClone'
// import deepMerge from '../../utils/deepMerge'

export default {
  data () {
    return {
      isPass: false,
      nextAssignmentUserNames: [],
      LoadingUtil
    }
  },
  props: {
    options: {
      type: Object,
      require: true
    },
    nextActivities: {
      type: Array,
      require: true
    },
    currentUserTask: {
      type: Object,
      require: true
    },
    confirmBtnDisable: {
      type: Boolean,
      default: false
    }
  },
  mounted () {
    window.page = this
  },
  methods: {
    // -------------------- 自定义方法 ------------------------
    getParams () {
      throw new Error('prepareParams undefined')
    },
    sendParams (params) {
      throw new Error('sendParams undefined')
    },
    // ------------------- 通用方法 --------------------
    async $_assignmentsAndOpinion () {
      let assignmentsAndOpinion = {}
      if (this.$refs.container) assignmentsAndOpinion.assignments = await this.$refs.container.getNextTaskAssignments()
      assignmentsAndOpinion.assignments = assignmentsAndOpinion.assignments || []
      assignmentsAndOpinion.opinion = await this.$refs.opinionInput.getOpinion()
      if (this.$refs.carboncopy) {
        assignmentsAndOpinion.carbonCopyUserIds = this.$refs.carboncopy.getCarbonCopyUserIds()
      } else {
        assignmentsAndOpinion.carbonCopyUserIds = []
      }
      return assignmentsAndOpinion
    }
  }
}



