import ExtrapostAction from './actions/ExtrapostAction'
import ErrorUtil from '../utils/ErrorUtil'

export default async function (options) {
  const extrapost = new ExtrapostAction(options)
  try {
    await extrapost.init()
  } catch (e) {
    ErrorUtil.alertError(e)
  }
}



