import store from '../store/carboncopy'
import api from '../api/carboncopy'
import { mapActions } from 'vuex'

let _orgsPromise = null

const mutations = {
  setSelectedUsers (val) {
    this.selectedUsers = val
  },
  setOrgsData (val) {
    _orgsPromise.resolve()
    this.orgsData = val
  },
  setUsersData (val) {
    this.usersData = val
  },
  setIsCC (val) {
    this.isCC = val
  }
}

const actions = {
  async getOrgsData () {
    const orgsData = await api.getOrgs()
    this.setOrgsData(orgsData)
  },
  async getUsersData (orgId, page, size, searchText) {
    const usersData = await api.getUsers(orgId, page, size, searchText)
    this.setUsersData(usersData)
  },
  whenOrgsDataSetted (method) {
    _orgsPromise.then(method)
  }
}

export default {
  data () {
    return {
      isCC: false,
      vis: false,
      selectedUsers: [],
      orgsData: [],
      usersData: [],
      currentSelectedOrg: null
    }
  },
  methods: {
    ...mapActions([
      'whenLoginContextSetted'
    ]),
    getCarbonCopyUserIds () {
      return this.selectedUsers.map(user => user.userId)
    },
    ...mutations,
    ...actions
  },
  computed: {
    seletedUserNumber () {
      return this.selectedUsers.length
    }
  },
  created () {
    _orgsPromise = new (class {
      constructor () {
        this._solve = undefined
        this._promise = new Promise((resolve, reject) => {
          this._solve = resolve
        })
      }
      resolve () {
        this._solve()
      }
      then (method) {
        this._promise.then(() => {
          method()
        })
        return this
      }
    })()
  },
  async mounted () {
    this.$store.registerModule('carboncopy', store)

    this.whenLoginContextSetted(({ loginOrgId }) => {
      this.whenOrgsDataSetted(() => {
        const org = this.orgsData.find(org => org.orgId === loginOrgId)
        this.currentSelectedOrg = org
      })
    })

    const { processDefId, activityDefId } = this.$store.state.rawData.currentUserTask
    try {
      const isCC = await api.getIsCC(processDefId, activityDefId)
      this.setIsCC(isCC)
      const selectedUserIds = await api.getSelectedUsers(processDefId, activityDefId)
      const selectedUsers = selectedUserIds.map(id => ({userId: id, userName: ''}))
      if (selectedUsers && selectedUsers.length > 0) this.setSelectedUsers(selectedUsers)
    } catch (e) {
      console.debug('extProperties empty or error')
    }
  }
}



