import RestUtil from '../utils/RestUtil'
import ErrorUtil from '../../../utils/ErrorUtil'

export default {
  data () {
    return {
      users: [],
      headertext: 'header',
      isLoading: true,
      selectedOrg: null,
      orgs: [],
      isOrgLoading: false,
      group: {
        groupId: '',
        groupName: ''
      }
    }
  },
  computed: {
    style () {
      let style = {}
      switch (this.cardStyle) {
        default:
        case 'single': style.width = 'fit-content'; style.border = '0px'; break
        case 'multi': style.width = '300px'; break
      }
      return style
    },
    org () {
      if (!this.activity.participant.orgManualSelect) {
        return {
          orgId: this.activity.participant.orgId,
          orgName: this.activity.participant.orgName
        }
      } else {
        return this.selectedOrg
      }
    }
  },
  props: {
    cardStyle: {
      type: String,
      default: 'multi'
    },
    options: Object,
    activity: {
      type: Object,
      default () {
        return {
          activityDefId: '',
          activityDefName: '',
          orgId: '',
          orgName: '',
          groupId: '',
          groupName: ''
        }
      }
    },
    currentUserTask: {
      type: Object,
      require: true
    }
  },
  methods: {
    async lazyQueryUsers (keyword) {
      console.debug('lazyQuaryUsers:' + keyword)
      this.isLoading = true
      let nextActivityOrgId = this.org.orgId || this.activity.participant.orgId
      let currentUserTaskOrgId = this.currentUserTask.orgId || nextActivityOrgId
      try {
        this.users = await RestUtil.queryUser(keyword, currentUserTaskOrgId, nextActivityOrgId, this.group.groupId)
      } catch (e) {
        console.error(e)
        ErrorUtil.popError(new Error('获取可选用户信息失败：' + e.message))
      } finally {
        this.isLoading = false
      }
    },
    async lazyQueryOrgs (keyword) {
      console.debug('lazyQueryOrgs:' + keyword)
      this.isOrgLoading = true
      try {
        this.orgs = await RestUtil.queryOrg(keyword)
        this.orgs = this.orgs.filter(org => org.orgName.indexOf(keyword) !== -1)
      } catch (e) {
        console.error(e)
        ErrorUtil.popError(new Error('获取可选组织信息失败：' + e.message))
      } finally {
        this.isOrgLoading = false
      }
    }
  }
}



