<script>
import { Tree } from 'element-ui'
const _expandOrCollapseAllNote = (root, expanded) => {
  let nodes = root.childNodes
  nodes.forEach((element, index) => {
    nodes[index].expanded = expanded
    _expandOrCollapseAllNote(nodes[index], expanded)
  })
}

const _expandLevel = (root, level) => {
  if (root.level === level) {
    root.expanded = true
  } else {
    let nodes = root.childNodes
    nodes.forEach((element, index) => {
      _expandLevel(nodes[index], level)
    })
  }
}

const _expandSelfAndParents = (leaf) => {
  if (leaf && leaf.level !== 0) {
    if (!leaf.expanded) leaf.expanded = true
    if (leaf.parent) _expandSelfAndParents(leaf.parent)
  }
}

export default {
  render (h) {
    return (
      <div class="bpm-tree">
        <div {...{
          style: {
            'height': '36px'
          }
        }}>
          <el-input
            placeholder="请搜索您要的内容"
            v-model={ this.searchText }
            {...{
              style: {
                width: '100%'
              },
              nativeOn: {
                'keyup': (e) => {
                  if (e.keyCode === 13) {
                    this.currentHighlightSpanIndex += 1
                    this.focusOnHighlight()
                  }
                }
              }
            }}
            size="mini">
            <template slot="append">
              <el-button onClick={ () => {
                this.currentHighlightSpanIndex -= 1
                this.focusOnHighlight()
              } }>∧</el-button>
              <el-button onClick={ () => {
                this.currentHighlightSpanIndex += 1
                this.focusOnHighlight()
              } }>∨</el-button>
            </template>
          </el-input>
        </div>
        <el-scrollbar
          view-style={`height:${this.height - 38}px`}
          {...{
            class: {
              'inner-tree': true
            }
          }}>
          <el-tree
            style="width:fit-content;min-width:100%;"
            {...{
              props: {
                ...this.$props,
                'render-content': this._renderContent,
                'render-after-expand': false,
                'highlight-current': true
              },
              on: {
                ...this.$listeners,
                'current-change': this.currentChange
              }
            }}
            ref="tree"
          ></el-tree>
        </el-scrollbar>
      </div>
    )
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    ...Tree.props,
    height: Number,
    value: Object
  },
  data () {
    return {
      searchText: '',
      currentHighlightSpanIndex: null,
      nodesIncludeKeyword: []
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.$refs.tree.setCurrentKey(this.value[this.nodeKey])
    })
  },
  methods: {
    focusOnHighlight () {
      let node = this.currentHighlightSpan
      if (node == null) return
      let key = node.attributes.key.nodeValue
      let tNode = this.$refs.tree.getNode(key)
      if (tNode.parent) _expandSelfAndParents(tNode.parent)
      let click = document.createEvent('HTMLEvents')
      click.initEvent('click', true, true)
      node.dispatchEvent(click)

      node.scrollIntoView({
        behavior: 'auto',
        block: 'start',
        inline: 'nearest'
      })
    },
    expandLevel (level) {
      if (level !== 0) {
        _expandLevel(this.$refs.tree.root, level)
      }
    },
    expandOrCollapseAllNote (isExpand) {
      _expandOrCollapseAllNote(this.$refs.tree.root, true)
    },
    currentChange (val) {
      this.$emit('change', val)
    }
  },
  watch: {
    value (val) {
      this.$nextTick(() => {
        this.$refs.tree.setCurrentKey(val[this.nodeKey])
      })
    },
    searchText (val) {
      this.$nextTick(() => {
        let list = this.$el.querySelectorAll('font.include-keyword')
        list = Array.of(...list)
        this.nodesIncludeKeyword = list.map(node => node.parentElement)
        let keys = this.nodesIncludeKeyword.map(node => node.attributes.key.nodeValue)

        this.currentHighlightSpanIndex = 0
        this.focusOnHighlight()
        keys.forEach(key => {
          let node = this.$refs.tree.getNode(key)
          if (node.parent) _expandSelfAndParents(node.parent)
        })
      })
    },
    currentHighlightSpan (newVal, preVal) {
      if (preVal && preVal.firstElementChild) preVal.firstElementChild.className = 'include-keyword'
      if (newVal && newVal.firstElementChild) newVal.firstElementChild.className = 'include-keyword focus-include-keyword'
    }
  },
  computed: {
    _renderContent () {
      return (h, {data, node, store}) => {
        return (
          <span {...{
            attrs: {
              'key': node.key
            }
          }}>{
              this.searchText === ''
                ? node.label
                : node.label.indexOf(this.searchText) !== -1
                  ? <font class="include-keyword">{ node.label }</font>
                  : node.label
            }</span>
        )
      }
    },
    label () {
      return this.$attrs.props.label || 'label'
    },
    currentHighlightSpan () {
      let l = this.nodesIncludeKeyword.length
      if (l === 0) return null
      let i = (this.currentHighlightSpanIndex + 1) % l
      i = i > 0 ? i : i + l
      return this.nodesIncludeKeyword[i - 1]
    }
  }
}
</script>
<style lang="scss">
.include-keyword {
  font-weight: bold;
  background: #ffcc00;
}
.focus-include-keyword {
  background: #ff6600;
}
.bpm-tree .el-scrollbar {
  border: #dcdfe6 1px solid;
}
</style>



