<template>
  <div class="vote-container">
    <div style="margin-right:20px;">请选择你的意见</div>
    <el-button size="medium"
      @click="$emit('update:vote', SignOpinionType.AGREE)"
      :type="vote === SignOpinionType.AGREE ? 'primary': ''">同意</el-button>
    <el-button size="medium"
      @click="$emit('update:vote', SignOpinionType.DISAGREE)"
      :type="vote === SignOpinionType.DISAGREE ? 'primary': ''">不同意</el-button>
    <el-button size="medium"
      v-if="currentUserTask.behavior === MultiInstanceBehavior.SPECIFY_NUM ||
      currentUserTask.behavior === MultiInstanceBehavior.SPECIFY_PERCENT"
      @click="$emit('update:vote', SignOpinionType.ABSTAIN)"
      :type="vote === SignOpinionType.ABSTAIN ? 'primary': ''">弃权</el-button>
  </div>
</template>
<script>
import SignOpinionType from '../enumType/SignOpinionType'
import MultiInstanceBehavior from '../enumType/MultiInstanceBehavior'
export default {
  data () {
    return {
      SignOpinionType,
      MultiInstanceBehavior
    }
  },
  mounted () {
    window.container = this
  },
  props: {
    currentUserTask: {
      type: Object,
      require: true
    },
    vote: {
      type: Number,
      required: true,
      default: SignOpinionType.AGREE
    }
  },
  methods: {
    getNextTaskAssignments () {
      return null
    }
  }
}
</script>
<style lang="scss" scoped>
.vote-container {
  width: 100%;
  display: inline-flex;
  flex-flow: row wrap;
  align-items: center;
  align-content: stretch;
  justify-content: center;

  /deep/ .el-button:hover{
    border-color:#2190E2 !important;
  }
}
</style>



