import BpmnJS from 'bpmjs.viewer'

import ecpService from '../../../utils/ProxyService'
import ErrorUtil from '../../../utils/ErrorUtil'
import utils from 'ecp.utils'

export default {
  data () {
    return {
      utils: utils,
      // openType: 0,
      value: 1,
      viewer: undefined
    }
  },
  props: {
    options: Object,
    processDefName: String,
    containerId: {
      type: String,
      default: 'canvas'
    }
  },
  mounted () {
    window.processGraph = this

    // this.openType = 1
    this.getGraphXml(this.options, this.getCallBack)
  },
  methods: {
    /**
     * 获取流程图XML.
     */
    getGraphXml (options, callback) {
      let url = 'necp/mapp/bpm-engine/service/processGraph'
      let processDefName = options.processDefName
      let processDefId = options.processDefId
      let taskId = options.taskId
      let processInstId = options.processInstId
      if (processDefName) {
        url = url + '/processDefName/' + processDefName
      } else if (processDefId) {
        url = url + '/processDefId/' + processDefId
      } else if (taskId) {
        url = url + '/taskId/' + taskId
      } else if (processInstId) {
        url = url + '/processInstId/' + processInstId
      }
      ecpService.doGet(url, null, 'text')
        .then(resp => {
          callback && callback(resp.data)
        }).catch(e => {
          ErrorUtil.popError(e)
        })
    },
    /**
   * 服务端获取到XML后的回调函数.
   */
    getCallBack (xml) {
      // 获取流程id和流程名称
      var process = xml.match(/process id="(.*?)" name="(.*?)"/)
      this.processDefId = process[1]
      var processDefName = process[2]
      this.$emit('update:processDefName', processDefName)

      // BPMN查看器实例
      var vm = this
      vm.viewer = new BpmnJS({ container: '#' + this.containerId + 'Canvas' })
      // 导入BPMN 2.0流程图
      vm.viewer.importXML(xml, (err) => {
        if (err) {
          // 导入失败 ！
          console.error(err)
        } else {
          // 导入成功！
          vm.canvas = vm.viewer.get('canvas')
          vm.canvas.zoom('fit-viewport', true)

          vm.initSlider(xml, vm.viewer)
        }
      })
    },
    /**
     * 待办居中显示.
     */
    centerTodo (xml) {
    },
    initSlider (xml, viewer) {
      // 获取流程id和流程名称
      var matrix = document.getElementsByClassName('viewport')[0].getAttribute('transform')
      this.value = parseFloat(matrix.match(/matrix\((.*?)\)/)[1].split(' ')[0].substring(0, 3))
    },
    inputSlider () {
      this.canvas && this.canvas.zoom(this.value)
    }
  }
}



