<template>
  <!-- 模态窗内容 -->
  <div class="dialog-content">
    <!-- 当前环节 -->
    <!-- <div style="font-size:12px;position:absolute;left:32px;top:-35px;color:#999999;">
      当前环节<br><div class="ellipsis" style="font-size:13px;width:100px;">{{ currentUserTask.activityDefName }}</div>
    </div> -->
    <!-- 背景图 -->
    <!-- <div class="fly"><fly-svg></fly-svg></div> -->
    <!-- 下一环节 -->
    <!-- <div style="font-size:12px;position:absolute;right:32px;top:-35px;text-align:right;color:#999999;"
      >下一环节<br
      ><strong class="ellipsis" style="font-size:13px;color:#666666;width:100px;"
      >协办</strong>
    </div> -->
    <!-- 抄送 -->
    <div class="cc"><carbon-copy ref="carboncopy"></carbon-copy></div>
    <div name="pass-content">
      <div class="wrapper">
        <participants-container
          :options="options"
          :currentUserTask="currentUserTask"
          :nextActivities="nextActivities"
          ref="container"
        ></participants-container>
      </div>
      <div class="opinion-input">
        <opinion-input
          :operationType="OperationType.HELP"
          placeholder="请审批"
          ref="opinionInput"
        ></opinion-input>
      </div>
    </div>
  </div>
</template>

<script>
import AbstractPage from './mixins/AbstractPage'

import ParticipantsContainer from '../containers/ParticipantsContainer'
// import FlySvg from '../components/svg/Fly'
import LastTask from '../components/LastTask/LastTask'
import LastTaskSvg from '../components/svg/LastTask'
import BpmnDialog from '../components/BpmnDialog/BpmnDialog'
import OpinionInput from '../components/OpinionInput/OpinionInput'
import FinishPage from '../components/FinishPage/FinishPage'
import CarbonCopy from '../components/CarbonCopy/pc/CarbonCopy'

import TaskBizState from '../enumType/TaskBizState'
import OperationType from '../enumType/OperationType'

import ecpService from '../utils/ProxyService'
import Const from './Const'

export default {
  mixins: [AbstractPage],
  components: {
    ParticipantsContainer,
    // FlySvg,
    BpmnDialog,
    OpinionInput,
    LastTask,
    LastTaskSvg,
    FinishPage,
    CarbonCopy
  },
  data () {
    return {
      OperationType,
      TaskBizState
    }
  },
  props: {
  },
  methods: {
    async getParams () {
      console.debug('---get params---')
      let { assignments, opinion, carbonCopyUserIds } = await this.$_assignmentsAndOpinion()
      console.debug(assignments, opinion, carbonCopyUserIds)
      let params = {
        businessKey: this.options.businessKey,
        bizDataModel: this.options.bizDataModel,
        taskVariables: this.options.taskVariables,
        taskId: this.options.taskId
      }

      params.nextTaskAssignments = assignments
      params.nextTaskAssignment = assignments[0]
      params.opinion = opinion
      params.carbonCopyUserIds = carbonCopyUserIds

      return {
        params: params
      }
    },
    async sendParams (params) {
      delete params.nextTaskAssignments
      let url = Const.URL_extrapost
      console.debug('---sending params---')
      console.debug(params)
      let resp = await ecpService.doPost(url, params)
      console.debug(resp.data)
      return resp.data
    }
  }
}
</script>

<style lang="scss" scoped>
.dialog-content {
  margin-bottom: 0px;
  margin-top: 55px;
  position: relative;
}
.fly {
  position: absolute;
  width: 360px;
  left: 50%;
  margin-left: -180px;
  top: -35px;
  height: 21px;
}
.wrapper {
  width: 100%;
  min-height: 180px;
  max-height: 305px;
  overflow-y: auto;
  overflow-x: hidden;
}
.opinion-input {
  padding: 15px 32px;
  padding-top: 5px;
}
.cc {
  position: absolute;
  left: 50%;
  width: 100px;
  margin-left: -50px;
  top: -25px;
}
</style>



