import LoadingUtil from '../../utils/LoadingUtil'
import ErrorUtil from '../../utils/ErrorUtil'
import deepClone from '../../utils/deepClone'
import deepMerge from '../../utils/deepMerge'

export default {
  data () {
    return {
      visible: false,
      isPass: false,
      nextAssignmentUserNames: [],
      confirmBtnDisable: false,
      LoadingUtil
    }
  },
  props: {
    id: {
      type: String
    },
    options: {
      type: Object,
      require: true
    },
    nextActivities: {
      type: Array,
      require: true
    },
    currentUserTask: {
      type: Object,
      require: true
    }
  },
  mounted () {
    window.instance = this
  },
  methods: {
    // -------------------- 自定义方法 ------------------------
    prepareParams (rawParams) {
      throw new Error('prepareParams undefined')
    },
    sendParams (params) {
      throw new Error('sendParams undefined')
    },
    // ------------------- 通用方法 ---------------------------
    async action () {
      const freezing = LoadingUtil.freeze(this.$el)
      try {
        let { params, stopPassing } = await this.prepareParams()
        // 主动终止传递
        if (stopPassing) return

        // 传递前事件
        let isContinue = await this.$_beforeConfirmEvent(params)
        if (!isContinue) {
          return
        }
        // 获取指派用户名称
        this.nextAssignmentUserNames = params.nextTaskAssignments.map(assignment => assignment.username)

        let result = await this.$refs.page.sendParams(params)
        this.$_afterSend(params, result)
      } catch (e) {
        ErrorUtil.popError(e)
      } finally {
        freezing.unfreeze()
      }
    },
    async doClose () {
      this.options.afterClose && await this.options.afterClose(this.isPass)

      if (this.isPass && this.options.autoCloseFormPage) {
        window.open('about:blank', '_top')
        window.close()
      }
    },
    async $_afterSend (params, result) {
      this.$_cleanExtend()

      // 显示传递成功界面
      this.isPass = true
      // 传递成功后事件
      this.options.afterConfirm && this.options.afterConfirm(params, result)
      this.$_boardcast()
    },
    async $_beforeConfirmEvent (params) {
      // 传递前事件
      if (this.options.beforeConfirm != null) {
        let container = document.getElementById('extend')
        let beforeConfirmEventParam = {
          requestParam: deepClone(params),
          container: container
        }
        let {isContinue, result} = await this.options.beforeConfirm(beforeConfirmEventParam)
        // 中断
        if (!isContinue) return false
        if (result) {
          params.opinion = result.opinion || params.opinion
          deepMerge(params.taskVariables, result.taskVariables || {})
        }
        return true
      }
    },
    /**
     * 清空扩展区域
     */
    $_cleanExtend () {
      let extend = document.getElementById('extend')
      if (extend) extend.innerHTML = ''
    },
    $_boardcast () {
      // 刷新代办消息
      /* eslint-disable */
      if ($ && $.jStorage) {
        $.jStorage.set('bpm.taskTodoTask.refresh', '发送消息成功' + new Date().getTime())
        $.jStorage.set('bpm.taskExecution.unRegist', '发送消息成功' + new Date().getTime())
      }
    }
  }
}



