import bpmTree from '../../../bpm.tree/bpm.tree.vue'
import { flat2Tree } from '../../../bpm.tree/flatTreeTrans'
import bpmGrid from '../../../bpm.gird/bpm.gird.vue'

import mixin from '../../mixin/carbonCopy'

export default {
  mixins: [mixin],
  components: {
    'bpm-tree': bpmTree,
    'bpm-grid': bpmGrid
  },
  data () {
    return {
      selectedUsersDialogVisible: false,
      defaultExpandedKeys: [],
      editselectedUsers: []
    }
  },
  computed: {
    treeData () {
      return flat2Tree({ idname: 'orgId', pidname: 'pid', childrenname: 'children' }, this.orgsData)
    },
    gridData () {
      return this.usersData
    },
    number () {
      return this.selectedUsers.length
    },
    selectedUserNames () {
      return this.selectedUsers.map(user => user.userName).join(',')
    }
  },
  methods: {
    // remoteUsersData ({ page, size, searchText }) {
    //   this.getUsersData(this.currentSelectedOrg.orgId, page, size, searchText)
    // }
    editSelectedValues () {
      this.selectedUsers = this.editselectedUsers
      this.selectedUsersDialogVisible = false
    },
    showSelectedUsersDialog () {
      this.selectedUsersDialogVisible = true
      this.editselectedUsers = this.selectedUsers.slice(0)
    }
  },
  mounted () {
    window.cc = this

    this.getOrgsData()
    this.whenOrgsDataSetted(() => {
      setTimeout(() => {
        this.defaultExpandedKeys.push(this.currentSelectedOrg.orgId)
      }, 10)
    })
  },
  watch: {
    currentSelectedOrg (val) {
      this.getUsersData(val.orgId)
    }
  }
}



