<template>
  <el-dialog
    :id="id"
    class="bpmn-dialog"
    :visible.sync="vis"
    append-to-body
    width="680px"
    :close-on-click-modal="false"
    @close="$emit('close')">
    <!-- 模态窗头部 -->
    <div slot="title">
      <div class="headers">
        <div class='title-number'
          ><i
            class="iconfont icon-number"
            style="color:orange;margin-right:5px;font-size:14px;"></i
          >{{ processInstId ? 'NO.' + processInstId : '' }}</div>
      </div>
    </div>
    <!-- 模态窗内容 -->
    <div class="content">
      <slot></slot>
      <!-- 自定义区域 -->
      <div id="extend"></div>
    </div>
    <!-- 模态窗脚部 -->
    <div slot="footer">
      <slot name="footer"></slot>
    </div>
  </el-dialog>
</template>
<script>
export default {
  props: {
    id: {
      type: String,
      default: 'id'
    },
    isPass: {
      type: Boolean,
      required: true
    },
    processInstId: {
      type: String,
      default: ''
    },
    visible: {
      type: Boolean,
      required: true
    }
  },
  data () {
    return {
      vis: false
    }
  },
  methods: {},
  watch: {
    visible (val) {
      this.vis = val
    },
    vis (val) {
      this.$emit('update:visible', val)
    }
  }
}
</script>
<style lang="scss">
.bpmn-dialog {
  * {
    box-sizing: border-box;
  }
  .el-dialog__header {
    border-bottom: 1px solid #e5e5e5!important;
    padding: 10px!important;
  }
  .el-dialog__headerbtn {
    top: 15px!important;
    right: 15px!important;
  }
  .content {
    position: relative;
  }
  .el-dialog__body {
    padding: 0px!important;
  }
  .headers {
    margin: 5px;
  }
  .title-label {
    text-align: center;
    font-family: Microsoft YaHei, SimSun !important;
    font-weight: 300 !important;
  }
  .title-number {
    font-size: 14px;
    color: #666666;
    font-family: Microsoft YaHei, SimSun;
  }
  .el-dialog__footer {
    padding: 15px 15px!important;
    border-top: 1px solid #e5e5e5!important;
  }
}
</style>



