import ProcessGraphCanvas from '../ProcessGraphCanvas.vue'
export default {
  components: {
    ProcessGraphCanvas
  },
  data () {
    return {
      visible: false,
      processDefName: '',
      options: undefined,
      style: undefined
    }
  },
  mounted () {
    this.style = this.options.fullscreen ? {height: window.innerHeight - 187 + 'px'} : { height: this.options.height || '400px' }
  }
}



