<template>
  <el-row type="flex" align="middle" class="reminder">
    <el-col :span="2.5"><div>订阅流程环节：</div></el-col>
    <el-col :span="4.5">
      <div class="message-type-switchers"
        >邮件<el-switch v-model="switchEmail"></el-switch
        >微信<el-switch v-model="switchWX"></el-switch
      ></div
    ></el-col>
    <el-col :span="17">
      <el-select
        style="width:270px;"
        v-model="items"
        multiple
        :loading="options.length === 0"
        size="small"
        value-key="activityDefId"
        collapse-tags
        placeholder="请选择订阅环节">
        <el-option
          v-for="item in options"
          :key="item.activityDefId"
          :label="item.label"
          :value="item">
        </el-option>
      </el-select>
    </el-col>
  </el-row>
</template>
<script>
import Reminder from './js/Reminder.js'
export default Reminder
</script>
<style lang="scss" scoped>
/deep/ .message-type-switchers .el-switch {
  margin-left: 5px;
  margin-right: 10px;
}
.reminder {
  width: 100%;
  height: 40px;
}
</style>



