import start from './start.js'
import complete from './complete.js'
import rollback from './rollback.js'
import help from './help.js'
import extrapost from './extrapost.js'

function taskExecute (options) {
  _checkOptions(options)

  // 根据options.action选择组件
  if (options.action === 'start') start(options)
  else if (options.action === 'complete') complete(options)
  else if (options.action === 'rollback') rollback(options)
  else if (options.action === 'help') help(options)
  else if (options.action === 'extrapost') extrapost(options)
}

/**
 * 检查传入参数.
 */
function _checkOptions (options) {
  if (['start', 'complete', 'rollback', 'help', 'extrapost'].indexOf(options.action) === -1) throw new Error('action参数非法')
}

export default taskExecute



