import AbstractAction from './AbstractAction'

import SelectType from '../../enumType/SelectType'

// import ecpService from 'ecp.service'
import ecpService from '../../utils/ProxyService'
import Const from '../Const'

class ExtrapostAction extends AbstractAction {
  constructor (options) {
    super(options, 'extrapost')
  }

  /**
   * 检查传入参数
   * @param {Object} options
   */
  checkOptions (options) {
    if (options.action !== 'extrapost') {
      throw new Error('action must be extrapost')
    }
    if (options.taskId == null || options.taskId === '') {
      throw new Error('taskId 为空')
    }
  }

  /**
   * 格式化数据
   * @param {Object} data
   */
  formatDate (data) {
    console.debug('---format data---')
    console.debug(data)
    let currentUserTask = data
    let nextActivities = [
      {
        activityDefName: '',
        participant: {
          'orgId': currentUserTask.orgId,
          'defaultUserId': '',
          'defaultUserName': '',
          participantDef: {
            // 仅用户
            selectType: SelectType.SELECT_USER_TYPE
          }
        }
      }
    ]

    return {
      currentUserTask: currentUserTask,
      nextActivities: nextActivities
    }
  }

  /**
   * abstract created
   * @param {Object} instance
   */
  created (instance) {
    instance.visible = true
  }

  /**
   * 请求数据
   * @param {Object} options
   */
  async queryInfo (options) {
    let resp = await ecpService.doGet(Const.URL_canExtrapost + '?taskId=' + options.taskId)
    if (!resp.data.success) throw new Error(resp.data.message)

    let url = Const.URL_findByTaskId + options.taskId
    resp = await ecpService.doGet(url)
    return resp.data
  }

  /**
   * root store
   */
  getRootStore () {
    return {}
  }
}

export default ExtrapostAction



