import ExtendEventFactory from '../extend/extendEventFactory'
export default function Events () {
  return {
    /**
     * @param {object} params 查询下一环节信息的返回
     */
    afterRender (params) {
      console.debug('---after render---')
      console.debug(params)
    },
    /**
     * 传递前事件
     * @param {object} params 事件参数
     */
    async beforeConfirm (params) {
      console.debug('---before confirm---')
      console.debug(params)
      return {
        isContinue: true,
        result: null
      }
    },
    /**
     * 传递后事件
     * @param {object} params 调用接口参数
     * @param {object} result 结果返回
     */
    afterConfirm (params, result) {
      console.debug('---after confirm---')
      ExtendEventFactory.register('auto-open-next-task', params, result)
      // ExtendEventFactory.register('store-last-finished-task', params, result)
    },
    /**
     * 窗体关闭后的回调
     * @param {是否处于传递成功的状态} success
     */
    afterClose (success) {
      console.debug('---after close---')
      console.debug('is passed:' + success)
    }
  }
}



