<template>
  <div class="opinion-input-style">
    <el-autocomplete
      popper-class="my-autocomplete-popper"
      v-model="inputtext"
      size="medium"
      :maxlength="250"
      :fetch-suggestions="querySearch"
      :placeholder="placeholder"
      @input="handleSelect"
      value-key="opinion">
      <el-button type="text" slot="suffix" v-show="isNewOpinion" @click="storeUserOpinion" size="medium">加入常用列表</el-button>
      <template slot-scope="{ item }">
        <div class="opinion">
          <div class="opinion--text">{{ item.opinion }}</div>
          <div class="opinion--buttons">
            <!-- onClick="event.cancelBubble = true" 防止按钮点击事件触发了父级的onclick事件 -->
            <el-button type="text" onClick="event.cancelBubble = true" @click="deleteUserOpinion(item.gid)">删除</el-button>
            <el-button type="text" v-if="!item.defaultStatus" onClick="event.cancelBubble = true" @click="updateDefaultUserOpinion(item.gid)">设为默认</el-button>
          </div>
        </div>
      </template>
    </el-autocomplete>
  </div>
</template>
<script>
import Const from './Const'
import ecpService from '../../utils/ProxyService'
import ErrorUtil from '../../utils/ErrorUtil'

export default {
  data () {
    return {
      inputtext: '',
      errorshow: false,
      opinions: [],
      isNewOpinion: false
    }
  },
  mounted () {
    window.opinion = this
    this.findAll()
  },
  watch: {
    inputtext (val) {
      // if (this.errorshow === true) this.errorshow = false
      if (val !== '' && !this.opinions.find(ele => ele.opinion === val)) {
        this.isNewOpinion = true
      } else {
        this.isNewOpinion = false
      }
    }
  },
  props: {
    placeholder: String,
    operationType: Number,
    defaultOpinion: String
  },
  methods: {
    /**
     * 检测特殊字符.
     */
    checklsHasSpecialStr (opinion) {
      var check = /[+\\\\#=&'^<> ]/
      return check.test(opinion)
    },
    querySearch (queryString, cb) {
      var opinions = this.opinions
      var results = queryString ? opinions.filter(this.createFilter(queryString)) : opinions
      // 调用 callback 返回建议列表的数据
      cb(results)
      cb(this.opinions)
    },
    createFilter (queryString) {
      return (opinion) => {
        return (opinion.opinion.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    findAll () {
      var url = Const.URL_findAllUserOpinion + '?operationType=' + this.operationType
      ecpService.doGet(url)
        .then(resp => {
          if (resp.headers && resp.headers.error_code) throw resp.data.errMsg || '未知错误'
          this.opinions = resp.data
          if (this.opinions.length === 0 || !this.opinions.find(ele => ele.defaultStatus)) {
            this.inputtext = this.placeholder
          } else if (this.opinions.find(ele => ele.defaultStatus)) {
            this.inputtext = this.opinions.find(ele => ele.defaultStatus).opinion
          }
          if (this.defaultOpinion) this.inputtext = this.defaultOpinion
        })
        .catch(e => {
          this.$message({
            customClass: 'mzindex',
            showClose: true,
            title: '提示',
            message: e,
            type: 'error'
          })
          console.error(e)
        })
    },
    deleteUserOpinion (gid) {
      var url = Const.URL_deleteUserOpinion(gid)
      ecpService.doPost(url)
        .then(resp => {
          if (resp.headers && resp.headers.error_code) throw resp.data.errMsg || '未知错误'
          this.opinions.splice(this.opinions.findIndex(ele => ele.gid === gid), 1)

          // 重新检查当前意见是否不存在于意见列表
          if (this.inputtext !== '' && !this.opinions.find(ele => ele.opinion === this.inputtext)) {
            this.isNewOpinion = true
          }
        })
        .catch(e => {
          this.$message({
            customClass: 'mzindex',
            showClose: true,
            title: '提示',
            message: e,
            type: 'error'
          })
          console.error(e)
        })
    },
    updateDefaultUserOpinion (gid) {
      var url = Const.URL_updateDefaultUserOpinion(gid)
      ecpService.doPost(url)
        .then(resp => {
          if (resp.headers && resp.headers.error_code) throw resp.data.errMsg || '未知错误'
          var df = this.opinions.find(ele => ele.defaultStatus)
          if (df) {
            df.defaultStatus = false
          }
          this.opinions.find(ele => ele.gid === gid).defaultStatus = true
        })
        .catch(e => {
          this.$message({
            customClass: 'mzindex',
            showClose: true,
            title: '提示',
            message: e,
            type: 'error'
          })
          console.error(e)
        })
    },
    storeUserOpinion () {
      // opinion储存
      // 校验是否非法
      if (this.checklsHasSpecialStr(this.inputtext)) {
        ErrorUtil.popError(new Error('留言包含特殊字符'))
        return
      }
      // 校验是否已存在
      if (!this.opinions.find(ele => ele.opinion === this.inputtext)) {
        let isFirst = this.opinions.length === 0
        this.createHistoryOpinion(this.operationType, this.inputtext, isFirst, (data) => {
          this.opinions.unshift(data)
          this.isNewOpinion = false
        })
      }
    },
    createHistoryOpinion (operationType, opinion, isFirst, callback) {
      var url = Const.URL_createUserOpinion
      ecpService.doPost(url, {
        'operationType': operationType,
        'opinion': opinion,
        'defaultStatus': isFirst
      }).then(resp => {
        if (resp.headers && resp.headers.error_code) throw resp.data.errMsg || '未知错误'
        callback && callback(resp.data)
      }).catch(e => {
        this.$message({
          customClass: 'mzindex',
          showClose: true,
          title: '提示',
          message: e,
          type: 'error'
        })
        console.error(e)
      })
    },
    getOpinion () {
      if (this.checklsHasSpecialStr(this.inputtext)) {
        throw new Error('留言包含特殊字符')
      }

      return this.inputtext
    },
    handleSelect (item) {}
  }
}
</script>
<style lang="scss" scoped>
.opinion-input-style {
  position: relative;
}

.opinion-input-style .el-autocomplete {
  width: 100%;
  .el-input__suffix .el-button {
    display: none;
    padding: 7px 15px;
    padding-left: 0px;
    line-height: normal;
  }
  .el-input--suffix .el-input__inner {
    padding-right: 114px;
  }
  .el-input--suffix:hover .el-input__suffix .el-button{
    display: inline;
  }
}

.my-autocomplete-popper {
  li {
    line-height: normal;
    padding: 0 20px;
    margin: 0;
    color: #606266;
    font-size: 14px;
    .opinion {
      padding: 8px 0px;
      display: flex;
      align-items: flex-start;
      .opinion--text {
        white-space: normal;
        word-wrap: break-word;
        word-break: break-all;
        line-height: 21px;
        width: 80%;
      }
      .opinion--buttons {
        width: 20%;
        align-self: flex-end;
        .el-button {
          display: none;
          float: right;
          padding: 0px;
        }
        .el-button+.el-button {
          margin: 0px 10px;
        }
      }
    }
  }
  li:hover .opinion .opinion--buttons .el-button {
    display: inline;
  }
}
</style>



