<template>
  <div v-show="isCC">
    <el-tooltip placement="top" :content="selectedUserNames" effect="light" trigger="hover">
      <div><el-button @click="vis = true" type="text">已抄送&nbsp;<span class="cc-number">{{number}}</span></el-button></div>
    </el-tooltip>
    <el-dialog
      :visible.sync="vis"
      width="970px"
      append-to-body
      :close-on-click-modal="false">
      <div class="title" slot="title">抄送选择</div>
      <el-row :gutter="20">
        <el-col :span="8" style="height:100%;">
          <bpm-tree
            :height="330"
            :props="{
              children: 'children',
              label: 'orgName'
            }"
            :default-expanded-keys="defaultExpandedKeys"
            node-key="orgId"
            :data="treeData"
            v-model="currentSelectedOrg"
            ref="tree"></bpm-tree>
        </el-col>
        <el-col :span="16" style="height:100%;">
          <bpm-grid
            :height="330"
            border
            pageable
            selection
            :props="{
              'row-key': 'userId',
              'filter-attributes': ['userName', 'loginName', 'userOrgId']
            }"
            v-model="selectedUsers"
            :data="gridData"
            ref="grid">
            <el-table-column
              prop="userName"
              label="用户名称"
            ></el-table-column>
            <el-table-column
              prop="loginName"
              label="登录名称"
            ></el-table-column>
            <el-table-column
              prop="userOrgId"
              label="所属组织编号"
            ></el-table-column>
          </bpm-grid>
        </el-col>
      </el-row>
      <div slot="footer">
        <el-button type="text" @click="showSelectedUsersDialog" style="float:left;">已选{{number}}条</el-button>
        <el-button size="medium" @click="vis = false">取消</el-button>
        <el-button size="medium" type="primary" @click="vis = false">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog
      :visible.sync="selectedUsersDialogVisible"
      width="600px"
      append-to-body
      :close-on-click-modal="false">
      <div slot="title" class="title">已选用户</div>
      <bpm-grid
        :height="330"
        selection
        border
        v-model="editselectedUsers"
        :props="{
          'row-key': 'userId',
          'filter-attributes': ['userName', 'loginName', 'userOrgId']
        }"
        :data="selectedUsers"
        ref="selectedgrid">
        <el-table-column
          prop="userName"
          label="用户名称"
        ></el-table-column>
        <el-table-column
          prop="loginName"
          label="登录名称"
        ></el-table-column>
        <el-table-column
          prop="userOrgId"
          label="所属组织编号"
        ></el-table-column>
      </bpm-grid>
      <div slot="footer">
        <el-button size="medium" type="primary" @click="editSelectedValues">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import CarbonCopy from './js/CarbonCopy'
export default CarbonCopy
</script>
<style lang="scss" scoped>
.cc-number {
  background: #FFBA00;
  color: white;
  font-weight: bold;
  padding: 1px 5px;
  border-radius: 10px;
}
.title {
  font-size: 16px;
  color: #409EFF;
}
/deep/ .el-dialog__header {
  border-bottom: 1px solid #e5e5e5!important;
}
/deep/ .el-dialog__footer {
  border-top: 1px solid #e5e5e5!important;
}
/deep/ .el-table td, .el-table th {
  padding: 6px 0;
}
</style>



