import AbstractAction from './AbstractAction'

// import ecpService from 'ecp.service'
import ecpService from '../../utils/ProxyService'
// import utils from 'ecp.utils'
import Const from '../Const'

class RollbackAction extends AbstractAction {
  constructor (options) {
    super(options, 'rollback')
  }

  /**
   * 检查传入参数
   * @param {Object} options
   */
  checkOptions (options) {
    if (options.action !== 'rollback') {
      throw new Error('action must be rollback')
    }
    if (options.taskId == null || options.taskId === '') {
      throw new Error('taskId 为空')
    }
  }

  /**
   * 格式化数据
   * @param {Object} data
   */
  formatDate (data) {
    console.debug('---format data---')
    console.debug(data)
    let {nextUserTasks, currentUserTask, processInstId} = data
    currentUserTask.processInstId = processInstId

    if (nextUserTasks.length === 0) {
      throw new Error('没有可回退环节。')
    }

    nextUserTasks = nextUserTasks.map(task => {
      // 仅用户
      task.participant.participantDef = {
        ...task.participant.participantDef,
        selectType: '2'
      }
      return task
    })

    return {
      currentUserTask: currentUserTask,
      nextActivities: nextUserTasks
    }
  }

  /**
   * abstract created
   * @param {Object} instance
   */
  created (instance) {
    instance.visible = true
  }

  /**
   * 请求数据
   * @param {Object} options
   */
  async queryInfo (options) {
    let resp = await ecpService.doGet(Const.URL_canrollback + '?taskId=' + options.taskId)
    if (!resp.data.success) throw new Error(resp.data.message)

    let url = Const.URL_findRollbackNext + '?taskId=' + options.taskId
    resp = await ecpService.doGet(url)
    return resp.data
  }

  /**
   * root store
   */
  getRootStore () {
    return {}
  }
}

export default RollbackAction



