import EcpService from '../../../utils/ProxyService'
export default {
  async getActivityIdNameMap (processDefId) {
    let resp = await EcpService.doGet(`necp/mapp/bpm-engine/query/processDefinitions/processDefId/${processDefId}/userTasks`)
    return resp.data
  },
  async saveReminder (newRecord) {
    let resp = await EcpService.doPost(`necp/mapp/bpm-engine/service/processInstReminder/save`, newRecord)
    return resp.data
  },
  async getReminder (processInstId, userId) {
    let resp = await EcpService.doGet(`necp/mapp/bpm-engine/service/query/processInstReminder/${processInstId}/${userId}`)
    return resp.data
  }
}



