import ecpService from '../../../utils/ProxyService'
// import ecpService from 'ecp.service'
import LoginContextUtil from '../../../utils/LoginContextUtil'

export default {
  URL_participant_users: 'necp/mapp/bpm-engine/participant/users',
  URL_participant_orgs: 'necp/mapp/bpm-engine/participant/orgs',
  async queryUser (keyword, currentUserTaskorgId, orgId, groupId) {
    let context = await LoginContextUtil.getLoginContext()
    var queryParam = {
      conditions: [{
        conditionField: 'deptCompid',
        operator: '0',
        value: currentUserTaskorgId
      }],
      orgId: orgId,
      userName: keyword,
      userId: context.userId,
      size: 200
    }
    var url = this.URL_participant_users
    if (groupId) url += '/' + groupId

    let resp = await ecpService.doPost(url, queryParam)
    return resp.data
  },
  async queryOrg (keyword) {
    // let context = await LoginContextUtil.getLoginContext()
    var queryParam = {
      orgName: keyword,
      size: 200
    }
    var url = this.URL_participant_orgs

    let resp = await ecpService.doPost(url, queryParam)
    return resp.data
  },
  async findDefaultUser (options, manualOrgId, nextActDefId, participantDefId) {
    const URL = 'necp/mapp/bpm-engine/service/tasks/findManualOrgParticipant'
    let resp = await ecpService.doPost(URL, {
      taskId: options.taskId,
      taskVariables: options.taskVariables,
      bizDataModel: options.bizDataModel,
      manualOrgId: manualOrgId,
      nextActDefId: nextActDefId,
      participantDefId: participantDefId
    })
    return resp.data
  }
}



