<template>
  <div :class="{'sign-card': true, 'opacity': !checked}" :style="style" @click="() => {if(!checked) checked = true}">
    <!-- beader -->
    <div class="card-header" v-show="cardStyle==='single'?false:true">
      <label class="card-header-text">{{ activity.activityDefName }}</label>
      <el-checkbox v-if="!required" class="card-header-checkbox" size="mini" v-model="checked" onClick="event.cancelBubble = true" :disabled="required"></el-checkbox>
      <div v-if="required" class="biqian">
        <biqian-svg/>
      </div>
    </div>
    <div class="card-contianer">
      <div class="participant-svg">
        <participant-svg/>
      </div>
      <div style="max-width:80%;width:148px;">
        <!-- select -->
        <div class="textlabel verticalMiddle"
          v-show="showUserSelect">
          <el-select
            size="mini"
            v-model="selected"
            multiple
            filterable
            remote
            :disabled="required && defaultUsers.length > 0"
            placeholder="请选择"
            :loading="isLoading"
            @focus="() => {
              if (!checked) checked = true
              lazyQueryUsers('')
            }"
            :remote-method="lazyQueryUsers"
            value-key="userId"
            ref="select">
            <el-option
              v-for="item in users"
              :key="item.userId + (item.userDeptId?item.userDeptId:'')"
              :label="item.userName"
              :value="item">{{ group.groupName ? group.groupName + ' ' + item.userName : item.userName }}</el-option>
          </el-select>
        </div>
        <!-- group -->
        <div class="textlabel verticalMiddle"
          :title="group.groupName"
          v-if="group.groupName">
          <div class="textstyle">{{ group.groupName }}</div>
        </div>
        <!-- org -->
        <!-- <div class="textlabel verticalMiddle"
          :title="activity.participant.orgName"
          v-if="activity.participant.orgName">
          <div class="textstyle">{{ activity.participant.orgName }}</div>
        </div> -->
        <!-- org -->
        <div class="textlabel verticalMiddle">
          <div
            v-if="!activity.participant.orgManualSelect && org.orgName"
            :title="org.orgName"
            class="textstyle">{{ org.orgName }}</div>
          <el-select
            v-if="activity.participant.orgManualSelect"
            size="mini"
            v-model="selectedOrg"
            filterable
            remote
            placeholder="请选择组织"
            value-key="orgId"
            :loading="isOrgLoading"
            :remote-method="lazyQueryOrgs"
            @focus="() => {
              if (!checked) checked = true
              lazyQueryOrgs('')
            }">
            <el-option
              v-for="item in orgs"
              :key="item.orgId"
              :label="item.orgName"
              :value="item">{{ item.orgName }}</el-option>
          </el-select>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Const from './Const'
import Participant from './mixins/Participant'
import RestUtil from './utils/RestUtil'

import ParticipantSvg from '../svg/Participant'
import BiqianSvg from '../svg/Biqian'
import HistorySelectedUserMap from './utils/HistorySelectedUserMap'

export default {
  mixins: [Participant],
  components: {
    ParticipantSvg,
    BiqianSvg
  },
  data () {
    return {
      Const,
      selected: null,
      defaultUsers: [],
      headertext: 'header',
      checked: false,
      required: false
    }
  },
  props: {
  },
  async mounted () {
    // 默认传递用户
    if (!this.activity.participant.orgManualSelect) {
      this.setDefaultUser()
      // 获取角色
      this.group.groupId = this.activity.participant.groupId
      this.group.groupName = this.activity.participant.groupName
    }

    this.required = this.activity.participant.participantDef.required
    this.checked = this.cardStyle === 'single' || this.required
  },
  methods: {
    /**
     * 父组件调用 获取指派
     */
    getAssignment () {
      var selected = this.selected

      var assignments = []

      // 未选中返回空数组
      if (!this.checked) return []

      if (selected.length === 0) {
        // 不能为空
        throw new Error('用户不能为空')
      } else {
        for (let each of selected) {
          assignments.push({
            activityDefId: this.activity.activityDefId,
            participantType: Const.PARTICIPANT_USER_TYPE,
            orgId: this.org.orgId,
            participantDefId: this.activity.participant.participantDef.id,
            participantId: each.userId,
            username: each.userName
          })
        }
      }
      const historyUsers = assignments.map(v => ({ userId: v.participantId, userName: v.username }))
      HistorySelectedUserMap.setHistorySelectedUsers(
        {
          processDefId: this.currentUserTask.processDefId,
          activityDefId: this.activity.activityDefId,
          orgId: this.org.orgId,
          groupId: this.group.groupId,
          participantDefId: this.activity.participant.participantDef.id
        }, historyUsers)
      return assignments
    },
    async setDefaultUser () {
      if (!this.org.orgId) {
        console.error('no orgId')
      }
      // 优先级 1 仅存在一个可选用户
      await this.lazyQueryUsers('')
      if (this.users.length === 1) {
        console.debug('---默认用户 唯一用户---')
        this.selected = [this.users[0]]
        return
      }

      // 优先级 2 默认传递用户
      if (this.activity.participant.defaultUserId) {
        console.debug('---默认用户 建模默认---')
        let defaultUserNames = this.activity.participant.defaultUserName.split(',')
        let defaultUserIds = this.activity.participant.defaultUserId.split(',')
        this.defaultUsers = defaultUserIds.map((e, i) => ({
          userId: e,
          userName: defaultUserNames[i]
        }))
        this.selected = this.defaultUsers
        this.users = this.defaultUsers
        return
      } else if (this.activity.participant.orgManualSelect) { // 规则指定默认用户
        let participant = await RestUtil.findDefaultUser(this.options, this.org.orgId, this.activity.activityDefId, this.activity.participant.participantDef.id)
        this.group.groupId = participant.groupId
        this.group.groupName = participant.groupName
        if (participant.defaultUserName && participant.defaultUserId) {
          console.debug('---默认用户 建模默认---')
          let defaultUserNames = participant.defaultUserName.split(',')
          let defaultUserIds = participant.defaultUserId.split(',')
          this.defaultUsers = defaultUserIds.map((id, i) => ({
            userId: id,
            userName: defaultUserNames[i]
          }))
          this.selected = this.defaultUsers
          this.users = this.defaultUsers
          return
        }
      }

      // 优先级 3 历史选择过的用户
      if (this.currentUserTask.processDefId != null && this.currentUserTask.processDefId !== '') {
        let users = HistorySelectedUserMap.getHistorySelectedUsers({
          processDefId: this.currentUserTask.processDefId,
          activityDefId: this.activity.activityDefId,
          orgId: this.org.orgId,
          groupId: this.group.groupId,
          participantDefId: this.activity.participant.participantDef.id
        })
        if (users.length > 0) {
          console.debug('---默认用户 历史用户---')
          this.users = users
          this.selected = users
        }
      }
    }
  },
  watch: {
    checked (val) {},
    selectedOrg (val) {
      this.selected = []
      this.setDefaultUser()
    }
  },
  computed: {
    showUserSelect () {
      if (this.activity.participant.orgManualSelect && (this.selectedOrg == null || this.selectedOrg === '')) {
        return false
      }
      return true
    }
  }
}
</script>
<style lang="scss" scoped>
.sign-card {
  height: 130px;
  min-width: 300px;
  border: #BCBCBC solid 1px;
  border-radius: 3px;
  position: relative;
  /deep/ .el-select {
    max-height: 42px;
    .el-tag {
      margin: 0px;
      margin-right: 5px;
      font-size: 14px;
      padding: 0px;
      background-color: #fff;
      color: #606266;
      border: transparent;
      .el-select__tags-text {
        max-width: 168px;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space:nowrap
      }
    }
    .el-tag__close.el-icon-close {
      right: -3px;
      top: -1px;
    }
    .el-select__tags {
      width: 190px!important;
      max-width: none!important;
      max-height: 42px;
      overflow: auto;
    }
    .el-input.is-disabled .el-input__inner {
      background-color: inherit;
    }
    .el-select__tags::-webkit-scrollbar{
      width:4px;
      height:4px;
    }
    .el-select__tags::-webkit-scrollbar-track{
      background: #f6f6f6;
      border-radius:2px;
    }
    .el-select__tags::-webkit-scrollbar-thumb{
      background: #aaa;
      border-radius:2px;
    }
    .el-select__tags::-webkit-scrollbar-thumb:hover{
      background: #747474;
    }
    .el-select__tags::-webkit-scrollbar-corner{
      background: #f6f6f6;
    }
  }

  .card-contianer {
    display: flex;
    height: 100%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .card-contianer .el-input--mini .el-input__inner {
    height: 22px;
    line-height: 22px;
    font-size: 16px;
    font-weight: bolder;
    border-width: 0px;
    padding: 0px!important;
    color: #409EFF;
  }
  .card-contianer .el-input--mini .el-input__icon {
    line-height: 20px;
    width: 10px;
  }
  .card-header {
    height: 10px;
    width: 300px;
    line-height: 20px;
    padding: 4px 10px;
    vertical-align: middle;
    position: absolute;
    background-color: transparent;
  }
  .card-header-text {
    font-size: 10px;
    color: #999999;
  }
  .card-header-checkbox {
    position: absolute!important;
    right: 9px!important;
    top: 3px!important;
  }

  .card-header-checkbox .el-checkbox__inner:hover{
    border-color: #000000;
  }

  .textlabel {
    text-align: start;
    padding: 0px 0px 0px 8px;
    overflow: visible;
    position: relative;
    line-height: 22px;
  }

  .textstyle {
    text-align: start;
    font-size: 14px;
    font-family: 微软雅黑;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  .participant-svg {
    height: 38px;
    width: 38px;
    font-size:0px;
    margin-right: 10px;
  }
}

/deep/ .el-input--mini .el-input__inner {
  width: 170px;
  border-width: 0px;
  padding: 0px!important;
  font-size: 14px;
  color: #409EFF;
  background: transparent;
}

/deep/ input::-webkit-input-placeholder{
  color: #409EFF!important;opacity:1;
}

/deep/ input::-moz-placeholder{   /* Mozilla Firefox 19+ */
  color: #409EFF!important;opacity:1;
}

/deep/ input:-moz-placeholder{    /* Mozilla Firefox 4 to 18 */
  color: #409EFF!important;opacity:1;
}

/deep/ input:-ms-input-placeholder{  /* Internet Explorer 10-11 */
  color: #409EFF!important;opacity:1;
}

.opacity {
  opacity: 0.5;
  cursor: pointer;
}
.sign-card:hover {
  opacity: 1;
}
.biqian {
  position: absolute;
  width: 45px;
  height: 45px;
  top: -1px;
  right: 1px;
}

</style>



