import AbstractAction from './AbstractAction'

// import ecpService from 'ecp.service'
import ecpService from '../../utils/ProxyService'
// import utils from 'ecp.utils'
import Const from '../Const'

class StartAction extends AbstractAction {
  constructor (options) {
    super(options, 'start')
  }

  /**
   * 检查传入参数
   * @param {Object} options
   */
  checkOptions (options) {
    if (options.action !== 'start') {
      throw new Error('action must be start')
    }
    if (options.processDefName == null || options.processDefName === '') {
      throw new Error('processDefName 为空')
    }
  }

  /**
   * 格式化数据
   * @param {Object} data
   */
  formatDate (data) {
    console.debug('---format data---')
    console.debug(data)
    let {nextActivities, firstUserTask, processDefId} = data
    if (firstUserTask) {
      firstUserTask.orgId = firstUserTask.participant.orgId
    } else {
      firstUserTask = {}
      firstUserTask.activityDefName = ''
      firstUserTask.firstActivity = true
    }
    firstUserTask.processInstId = ''
    firstUserTask.processDefId = processDefId

    return {
      currentUserTask: firstUserTask,
      nextActivities: nextActivities
    }
  }

  /**
   * abstract created
   * @param {Object} instance
   */
  created (instance) {
    instance.visible = true
  }

  /**
   * 请求数据
   * @param {Object} options
   */
  async queryInfo (options) {
    let url = Const.URL_findStartNext
    let params = {
      bizDataModel: options.bizDataModel,
      taskVariables: options.taskVariables,
      processVariables: options.processVariables,
      processDefName: options.processDefName,
      completeFirstTask: options.completeFirstTask
    }
    let resp = await ecpService.doPost(url, params)
    return resp.data
  }

  /**
   * root store
   */
  getRootStore () {
    return {}
  }
}

export default StartAction



