<template>
  <div class="finish-page" :style="{ height: '145px '}">
    <div class="finish-svg">
      <finish-svg />
    </div>
    <div style="line-height:25px;"
      >{{getPrompt(mode, names)}}<br><strong
        v-for="(passName, index) in names"
        :key="index">{{index === 0 ? "" : "，"}}{{ passName }}</strong>
    </div>
  </div>
</template>
<script>
import CompletionState from '../../enumType/CompletionState'

import FinishSvg from '../svg/FinishSvg'

export default {
  components: {
    FinishSvg
  },
  data () {
    return {}
  },
  props: {
    passNames: {
      type: Array
    },
    mode: {
      type: String,
      default: 'normal'
    },
    signCompleteState: Number
  },
  mounted () {
    window.page = this
  },
  methods: {
    getPrompt (mode, names) {
      switch (mode) {
        default:
          if (names.length === 0) return '您的流程已传递成功'
          else if (names.length > 0) return '已成功传递给'
          break
        case 'rollback':
          return '您的单据已回退给'
        case 'sign':
          if (this.signCompleteState === CompletionState.ACHIEVE_ROLLBACK) {
            return '会签不通过，自动回退到上环节'
          } else if (this.signCompleteState === CompletionState.NOT_ACHIEVE) {
            return '您的意见已生效'
          }
          break
      }
    }
  },
  computed: {
    names () {
      return this.passNames.filter((element, index, self) => {
        return self.indexOf(element) === index
      })
    }
  }
}
</script>
<style>
.finish-page {
  margin-top: 72px;
  position: relative;
  text-align: center;
  font-size: 14px;
  color: #666666;
  padding: 0px 20px;
  margin-bottom: 72px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.finish-svg {
  width: 74px;
}
</style>



