import Vue from 'vue'
import Vuex from 'vuex'

import reminder from './modules/reminder'

import LoginContextPromise from '../../../utils/LoginContextPromise'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'
const state = {
  loginContextPromise: new LoginContextPromise(),
  processInstAndDefIdPromise: new (class {
    constructor () {
      this.status = 'pending'
      this._solve = undefined
      this._promise = new Promise((resolve, reject) => {
        this._solve = resolve
      })
    }
    resolve ({processInstId, processDefId}) {
      this.status = 'resolve'
      this._solve({processInstId, processDefId})
    }
    then (method) {
      this._promise.then(result => {
        method(result)
        return result
      })
      return this
    }
  })()
}

const mutations = {
  setProcessInstAndDefIdSetted (state, {processInstId, processDefId}) {
    state.processInstAndDefIdPromise.resolve({
      processInstId, processDefId
    })
  }
}

const actions = {
  whenLoginContextSetted ({ state }, cb) {
    state.loginContextPromise.then(cb)
  },
  whenProcessInstAndDefIdSetted ({ state }, cb) {
    state.processInstAndDefIdPromise.then(cb)
  }
}

export default new Vuex.Store({
  state,
  mutations,
  actions,
  modules: {
    reminder
  },
  strict: debug
})



