export default {
  /**
     * 未处理.
     */
  NOTDONE: 0,

  /**
     * 传递.
     */
  POST: 1,

  /**
     * 终止.
     */
  TERMINATE: 2,

  /**
     * 回退.
     */
  ROLLBACK: 3,

  /**
     * 撤回.
     */
  DRAWBACK: 4,

  /**
     * 加签.
     */
  EXTRA_POST: 5,

  /**
   * 加签回退.
   */
  EXTRA_ROLLBACK: 6,

  /**
     * 协办.
     */
  HELP: 7,

  /**
     * 提交协办确认.
     */
  SUBMIT_HELP_CONFIRM: 8,

  /**
     * 委托.
     */
  DELGATE: 9,

  /**
     * 委托收回.
     */
  DELGATE_WITHDRAW: 10
}



