import RollbackAction from './actions/RollbackAction'
import ErrorUtil from '../utils/ErrorUtil'

export default async function (options) {
  const rollback = new RollbackAction(options)
  try {
    await rollback.init()
  } catch (e) {
    ErrorUtil.alertError(e)
  }
}



