<template>
  <div class="default-card" :style="style">
    <!-- beader -->
    <div class="card-header" v-show="cardStyle==='single'?false:true">
      <label class="card-header-text">{{activity.activityDefName}}</label>
    </div>
    <div class="card-contianer">
      <div class="participant-svg">
        <participant-svg />
      </div>
      <div class="participant-info">
        <!-- select -->
        <div class="textlabel verticalMiddle user-select"
          v-show="showUserSelect">
          <!-- user -->
          <el-select
            size="mini"
            v-model="selectedUser"
            filterable
            remote
            placeholder="请选择"
            value-key="userId"
            :loading="isLoading"
            :remote-method="lazyQueryUsers"
            @focus="lazyQueryUsers('')"
            :style="userSelectStyle">
            <el-option-group
              class="line"
              v-if="selectType == Const.SELECT_DETAULT_TYPE || selectType === ''">
              <el-option
                :key="group.groupId"
                :label="group.groupName"
                :value="'role'">{{group.groupName}}</el-option>
            </el-option-group>
            <el-option
              v-for="item in users"
              :key="item.userId + (item.userDeptId?item.userDeptId:'')"
              :label="item.userName"
              :value="item">{{
                `${item.userDeptName?item.userDeptName+' ':''}${group.groupName?group.groupName+' ':''}${item.userName}`
              }}</el-option>
          </el-select>
        </div>
        <!-- group -->
        <div class="textlabel verticalMiddle"
          :title="group.groupName"
          v-if="group.groupName"
          v-show="selectType == Const.SELECT_ROLE_TYPE || (selectedUser !== 'role' && selectedUser != '')">
          <div class="textstyle">{{group.groupName}}</div>
        </div>
        <!-- org -->
        <div class="textlabel verticalMiddle">
          <div
            v-if="!activity.participant.orgManualSelect && org.orgName"
            :title="org.orgName"
            class="textstyle">{{ org.orgName }}</div>
          <el-select
            v-if="activity.participant.orgManualSelect"
            size="mini"
            v-model="selectedOrg"
            filterable
            remote
            placeholder="请选择组织"
            value-key="orgId"
            :loading="isOrgLoading"
            :remote-method="lazyQueryOrgs"
            @focus="lazyQueryOrgs('')">
            <el-option
              v-for="item in orgs"
              :key="item.orgId"
              :label="item.orgName"
              :value="item">{{ item.orgName }}</el-option>
          </el-select>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Const from './Const'
import Participant from './mixins/Participant'
import ParticipantSvg from '../svg/Participant'
import RestUtil from './utils/RestUtil'
import HistorySelectedUserMap from './utils/HistorySelectedUserMap'
import ErrorUtil from '../../utils/ErrorUtil'

export default {
  mixins: [Participant],
  components: {
    ParticipantSvg
  },
  data () {
    return {
      Const,
      selectedUser: null,
      // selectedOrg: null,
      selectType: 0
    }
  },
  props: {
    forbidUserSelect: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    style () {
      let style = {}
      switch (this.cardStyle) {
        default:
        case 'single': style.width = 'fit-content'; style.border = '0px'; break
        case 'multi': style.width = '300px'; break
      }
      return style
    },
    userSelectStyle () {
      if (this.forbidUserSelect) {
        return { 'pointer-events': 'none' }
      }
    },
    showUserSelect () {
      if (this.activity.participant.orgManualSelect && (this.selectedOrg == null || this.selectedOrg === '')) {
        return false
      } else {
        // eslint-disable-next-line
        return this.selectType != Const.SELECT_ROLE_TYPE
      }
    }
  },
  async mounted () {
    this.selectType = this.activity.participant.participantDef.selectType

    // 默认传递用户
    if (!this.activity.participant.orgManualSelect) {
      this.group.groupId = this.activity.participant.groupId
      this.group.groupName = this.activity.participant.groupName
      this.setDefaultUser()
    }
  },
  methods: {
    /**
     * 父组件调用 获取指派
     */
    getAssignment () {
      var me = this
      var selectedUser = me.selectedUser
      if (me.activity.participant.orgManualSelect && !this.org) {
        throw new Error('组织不能为空')
      }
      var assignment = {
        activityDefId: this.activity.activityDefId,
        orgId: this.org.orgId
      }

      var success = false
      // 仅用户
      // eslint-disable-next-line
      if (me.selectType == Const.SELECT_USER_TYPE) {
        if (selectedUser == null || selectedUser === '') {
          // 提示不能为空
          me.errortext = '用户不能为空。'
        } else {
          assignment.participantId = selectedUser.userId
          assignment.participantType = Const.PARTICIPANT_USER_TYPE
          assignment.username = selectedUser.userName
          success = true
        }
        // eslint-disable-next-line
      } else if (me.selectType == Const.SELECT_DETAULT_TYPE || me.selectType == null || me.selectType === '') {
        // 用户角色均可
        if (me.users.length === 0) {
          // 角色无用户
          me.errortext = '该角色下无用户,请确认配置。'
        }
        if (selectedUser == null || selectedUser === '') {
          // 提示不能为空
          me.errortext = '请选择角色或用户。'
        } else if (selectedUser === 'role') {
          assignment.participantId = me.group.groupId
          assignment.participantType = Const.PARTICIPANT_GROUP_TYPE
          assignment.username = me.group.groupName
          success = true
        } else {
          assignment.participantId = selectedUser.userId
          assignment.participantType = Const.PARTICIPANT_USER_TYPE
          assignment.username = selectedUser.userName
          success = true
        }
        // eslint-disable-next-line
      } else if (me.selectType == Const.SELECT_ROLE_TYPE) {
        // 仅角色
        assignment.participantId = me.group.groupId
        assignment.participantType = Const.PARTICIPANT_GROUP_TYPE
        assignment.username = me.group.groupName
        success = true
      }

      if (success) {
        // 记录历史用户
        HistorySelectedUserMap.setHistorySelectedUsers(
          {
            processDefId: this.currentUserTask.processDefId,
            activityDefId: this.activity.activityDefId,
            orgId: this.org.orgId,
            groupId: this.group.groupId
          },
          [{
            userName: assignment.username,
            userId: assignment.participantId
          }])
        return assignment
      } else {
        throw new Error(this.errortext)
      }
    },
    /**
     * 默认用户规则
     */
    async setDefaultUser () {
      if (!this.org.orgId) {
        console.error('no orgId')
      }
      // 非传递 直接退出
      if (!this.activity.activityDefId) {
        return
      }
      // 优先级 1 仅存在一个可选用户
      await this.lazyQueryUsers('')
      if (this.users.length === 1) {
        console.debug('---默认用户 唯一用户---')
        this.selectedUser = this.users[0]
        return
      }

      // 优先级 2 默认传递用户
      if (this.selectType !== Const.SELECT_ROLE_TYPE) {
        if (!this.activity.participant.orgManualSelect && this.activity.participant.defaultUserId) {
          console.debug('---默认用户 建模默认---')
          this.selectedUser = {
            userId: this.activity.participant.defaultUserId,
            userName: this.activity.participant.defaultUserName
          }
          this.users = [{
            userId: this.activity.participant.defaultUserId,
            userName: this.activity.participant.defaultUserName
          }]
          return
        } else if (this.activity.participant.orgManualSelect) {
          let participant = await RestUtil.findDefaultUser(this.options, this.org.orgId, this.activity.activityDefId)
          this.group.groupId = participant.groupId
          this.group.groupName = participant.groupName
          if (participant.defaultUserName && participant.defaultUserId) {
            console.debug('---默认用户 建模默认---')
            this.selectedUser = {
              userId: participant.defaultUserId,
              userName: participant.defaultUserName
            }
            this.users = [{
              userId: participant.defaultUserId,
              userName: participant.defaultUserName
            }]
            return
          }
        }
      }

      // 优先级 3 历史选择过的用户
      if (this.currentUserTask.processDefId != null && this.currentUserTask.processDefId !== '') {
        let users = HistorySelectedUserMap.getHistorySelectedUsers({
          processDefId: this.currentUserTask.processDefId,
          activityDefId: this.activity.activityDefId,
          orgId: this.org.orgId,
          groupId: this.group.groupId
        })
        if (users[0]) {
          console.debug('---默认用户 历史用户---')
          this.selectedUser = users[0]
        }
      }
    }
  },
  watch: {
    selectedOrg (val) {
      this.selectedUser = null
      try {
        this.setDefaultUser()
      } catch (e) {
        ErrorUtil.alertError(e)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.default-card {
  height: 130px;
  min-width: 300px;
  border: #BCBCBC solid 1px;
  border-radius: 3px;
  position: relative;
}

.card-contianer {
  display: flex;
  height: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
/deep/ .el-input--mini .el-input__inner {
  width: 170px;
  border-width: 0px;
  padding: 0px!important;
  font-size: 14px;
  color: #409EFF;
  background: transparent;
}
.user-select /deep/ .el-input--mini .el-input__inner {
  height: 22px;
  font-weight: bolder;
  line-height: 22px;
  font-size: 16px;
}
.user-select /deep/ .el-input--mini .el-input__icon {
  line-height: 20px;
  width: 10px;
}

/deep/ input::-webkit-input-placeholder{
  color: #409EFF!important;opacity:1;
}

/deep/ input::-moz-placeholder{   /* Mozilla Firefox 19+ */
  color: #409EFF!important;opacity:1;
}

/deep/ input:-moz-placeholder{    /* Mozilla Firefox 4 to 18 */
  color: #409EFF!important;opacity:1;
}

/deep/ input:-ms-input-placeholder{  /* Internet Explorer 10-11 */
  color: #409EFF!important;opacity:1;
}

.card-header {
  height: 10px;
  line-height: 20px;
  padding: 4px 10px;
  vertical-align: middle;
  position: absolute;
}
.card-header-text {
  font-size: 10px;
  color: #999999;
}

.textlabel {
  text-align: start;
  padding: 0px 0px 0px 8px;
  overflow: visible;
  position: relative;
}

.textstyle {
  text-align: start;
  font-size: 14px;
  font-family: 微软雅黑;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.participant-svg {
  height: 38px;
  width: 38px;
  font-size:0px;
  margin-right: 10px;
}

.participant-info {
  width: 170px;
  overflow: visible;
}

.line {
  border-bottom: 1px solid #E4E7ED;
}
</style>



