<template>
  <el-dialog
    id="processGraph"
    :fullscreen="options.fullscreen"
    :width="this.options.width || '900px'"
    :close-on-click-modal="false"
    :visible.sync="visible">
    <!-- 模态窗头部 -->
    <div slot="title">
      <div class="headers">
        <div>{{'查看流程图：' + processDefName}}</div>
      </div>
    </div>
    <div :style="style">
      <!-- 画布内容 -->
      <ProcessGraphCanvas
        :options="options"
        :processDefName.sync="processDefName"></ProcessGraphCanvas>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="visible = false" size="medium">关闭</el-button>
    </span>
  </el-dialog>
</template>
<script>
import ProcessGraphDialog from './js/processGraphDialog'
export default ProcessGraphDialog
</script>
<style lang="scss" scoped>
/deep/ .el-dialog__header {
  border-bottom: 1px solid #e5e5e5!important;
}
/deep/ .el-dialog__footer {
  border-top: 1px solid #e5e5e5!important;
}
.headers {
  color: #428bca;
}
</style>



