// reminder mixin
import { mapActions, mapMutations, mapState } from 'vuex'
// import ErrorUtil from '../../../utils/ErrorUtil'

export default {
  created () {
    this.whenLoginContextSetted(loginContext => {
      this.setUserId(loginContext.userId)
      this.whenProcessInstAndDefIdSetted(({ processInstId, processDefId }) => {
        this.setProcessInstId(processInstId)
        this.setProcessDefId(processDefId)

        this.getReminder().catch(e => {
          console.error(e)
          // ErrorUtil.alertError(new Error('获取环节订阅信息失败。'))
        })
        this.getActivityIdNameMap().catch(e => {
          console.error(e)
          // ErrorUtil.alertError(new Error('获取环节信息失败。'))
        })
      })
    })
  },
  methods: {
    ...mapActions([
      'whenLoginContextSetted',
      'whenProcessInstAndDefIdSetted'
    ]),
    ...mapMutations('reminder', [
      'setOriginData',
      'setMessageType',
      'setReminderItems',
      'setProcessInstId',
      'setProcessDefId',
      'setUserId'
    ]),
    ...mapActions('reminder', [
      'getReminder',
      'saveReminder',
      'getActivityIdNameMap'
    ])
  },
  computed: {
    switchEmail: {
      get () {
        return (this.messageType & 1) > 0
      },
      set (val) {
        if (val) {
          this.setMessageType(this.messageType | 1)
        } else {
          this.setMessageType(this.messageType & (~1))
        }
      }
    },
    switchWX: {
      get () {
        return (this.messageType & (1 << 1)) > 0
      },
      set (val) {
        if (val) {
          this.setMessageType(this.messageType | (1 << 1))
        } else {
          this.setMessageType(this.messageType & (~(1 << 1)))
        }
      }
    },
    items: {
      get () {
        return this.reminderItems
      },
      set (val) {
        this.setReminderItems(val)
      }
    },
    ...mapState('reminder', [
      'messageType',
      'reminderItems',
      'activityIdNameMap'
    ]),
    options () {
      return Object.entries(this.activityIdNameMap).map(([id, name]) => ({activityDefId: id, label: name})) || []
    }
  }
}



